import React, {FC} from 'react';
import {useHistory} from 'react-router-dom';
import classNames from 'classnames';
import {Routes} from '../app/routes';
import {ReactComponent as BackArrowIcon} from '../images/back-arrow.svg';
import {Spinner} from './Spinner.component';
import styles from './Screen.module.scss';

interface ScreenCompositionProps {
  Header: FC<HeaderProps>;
  Content: FC<ContentProps>;
  Footer: FC<FooterProps>;
}

interface ScreenProps {
  readonly withoutHeader?: boolean;
  readonly footerWithGradient?: boolean;
  readonly footerWithActions?: boolean;
  readonly isFetching?: boolean;
}

interface HeaderProps {
  readonly backTo?: Routes;
  readonly onBack?: () => void;
}

interface ContentProps {
  readonly additionalClasses?: string;
}

interface FooterProps {
  readonly centered?: boolean;
  readonly additionalClasses?: string;
}

export const Screen: FC<ScreenProps> & ScreenCompositionProps = ({
  children,
  withoutHeader = false,
  footerWithGradient = false,
  footerWithActions = false,
  isFetching = false,
}) => {
  return (
    <div className={classNames(
      styles.screen, {
        [styles.withoutHeader] : withoutHeader,
        [styles.footerWithGradient] : footerWithGradient,
        [styles.footerWithActions] : footerWithActions,
      })}
    >
      {children}
      <Spinner isLoading={isFetching}/>
    </div>
  );
};

const Header: FC<HeaderProps> = ({backTo, onBack, children}) => {
  const history = useHistory();
  const goBack = () => {
    !!onBack && onBack();
    !!backTo && history.push(backTo);
  };

  return (
    <div className={styles.headerContainer}>
      <div className={styles.header}>
        {(backTo || onBack) && (
          <BackArrowIcon className={styles.backIcon} onClick={goBack}/>
        )}
        {children}
      </div>
    </div>
  );
};

const Content: FC<ContentProps> = ({
  children,
  additionalClasses,
}) => {
  return (
    <div className={styles.contentContainer}>
      <div className={classNames(styles.content, additionalClasses)}>
        {children}
      </div>
    </div>
  );
};

const Footer: FC<FooterProps> = ({
  children,
  centered = false,
  additionalClasses,
}) => {
  return (
    <div className={styles.footerContainer}>
      <div className={classNames(
        styles.footer,
        additionalClasses,
        {
          [styles.footerCentered]: centered,
        })}
      >
        {children}
      </div>
    </div>
  );
};

Screen.Header = Header;
Screen.Content = Content;
Screen.Footer = Footer;
