import React, {FC} from 'react';
import {Route, Switch} from 'react-router-dom';
import {History} from 'history';
import {AppProviders} from './AppProviders.component';
import {Login} from '../pages/auth/Login.component';
import {Signup} from '../pages/auth/Signup.component';
import {Game} from '../pages/Game.component';
import {Personalize} from '../pages/onboarding/Personalize.components';
import {TeamComposition} from '../pages/onboarding/TeamComposition.component';
import {Checkin} from '../pages/onboarding/Checkin.component';
import {MainStory} from '../pages/onboarding/MainStory.component';
import {Profile} from '../pages/profile/Profile.component';
import {ChangeAvatar} from '../pages/profile/ChangeAvatar.component';
import {TeamProfile} from '../pages/profile/TeamProfile.component';
import {Contact} from '../pages/contact/Contact.component';
import {Home} from '../pages/home/Home.component';
import {InventoryDetails} from '../pages/home/InventoryDetails.component';
import {Mission} from '../pages/mission/Mission.component';
import {MissionCompleted} from '../pages/mission/MissionCompleted.component';
import {MissionStory} from '../pages/mission/MissionStory.component';
import {VrTask} from '../pages/mission/VrTask.component';
import {ArTask} from '../pages/mission/ArTask.component';
import {ReceivedInventories} from '../pages/mission/ReceivedInventories.component';
import {FinalStory} from '../pages/finishing/FinalStory.component';
import {FinalPage} from '../pages/finishing/FinalPage.component';
import {FinalEvent} from '../pages/finishing/FinalEvent.component';
import {Install} from '../pages/Install.component';
import {NotFound} from '../pages/NotFound.component';
import {Routes} from './routes';
import {ProtectedRoute} from './ProtectedRoute';
import {TimeTracker} from './TimeTracker.component';

interface AppProps {
  history: History;
}

export const App: FC<AppProps> = ({history}) => {
  return (
    <AppProviders history={history}>
      <TimeTracker history={history}/>
      <Switch>
        <Route exact path={Routes.Login} component={Login}/>
        <Route exact path={Routes.Signup} component={Signup}/>
        <ProtectedRoute exact path={Routes.Game} component={Game}/>
        <ProtectedRoute exact path={Routes.Home} component={Home}/>
        <ProtectedRoute exact path={Routes.Profile} component={Profile}/>
        <ProtectedRoute exact path={Routes.ProfileChangeAvatar} component={ChangeAvatar}/>
        <ProtectedRoute exact path={Routes.ProfileTeam} component={TeamProfile}/>
        <ProtectedRoute exact path={Routes.Contact} component={Contact}/>
        <ProtectedRoute exact path={Routes.OnboardingPersonalize} component={Personalize}/>
        <ProtectedRoute exact path={Routes.OnboardingCheckIn} component={Checkin}/>
        <ProtectedRoute exact path={Routes.OnboardingTeam} component={TeamComposition}/>
        <ProtectedRoute exact path={Routes.OnboardingMainStory} component={MainStory}/>
        <ProtectedRoute exact path={Routes.Mission} component={Mission}/>
        <ProtectedRoute exact path={Routes.MissionCompleted} component={MissionCompleted}/>
        <ProtectedRoute exact path={Routes.MissionStory} component={MissionStory}/>
        <ProtectedRoute exact path={Routes.VrTask} component={VrTask}/>
        <ProtectedRoute exact path={Routes.ArTask} component={ArTask}/>
        <ProtectedRoute exact path={Routes.InventoryDetails} component={InventoryDetails}/>
        <ProtectedRoute exact path={Routes.ReceivedInventories} component={ReceivedInventories}/>
        <ProtectedRoute exact path={Routes.FinalStory} component={FinalStory}/>
        <ProtectedRoute exact path={Routes.FinalPage} component={FinalPage}/>
        <ProtectedRoute exact path={Routes.FinalEvent} component={FinalEvent}/>
        <Route exact path="/" component={Install}/>
        <Route path="*" component={NotFound}/>
      </Switch>
    </AppProviders>
  );
};
