import React, {ChangeEvent, FC, FormEvent, useEffect, useRef, useState} from 'react';
import {useHistory} from 'react-router-dom';
import {Screen} from '../../common/Screen.component';
import {Title} from '../../common/Title.component';
import {TextInput} from '../../common/TextInput.component';
import {Message, MessageType} from '../../common/Message.component';
import {Routes} from '../../app/routes';
import {ProfileError, ProfileErrorKey, useProfileStore} from '../onboarding/Profile.store';
import {AvatarIcon, AvatarIconTypes} from '../../common/AvatarIcon.component';
import {Button, ButtonTypes} from '../../common/Button.component';
import {Accordion} from '../../common/Accordion.component';
import {ReactComponent as LockIcon} from '../../images/lock_filled.svg';
import styles from './Profile.module.scss';

export const Profile: FC = () => {
  const history = useHistory();
  const ref = useRef<HTMLInputElement>(null);
  const {nickname, changeNickname, email, avatar, changePassword, error, passwordWasChanged, isProfileStoreFetching} = useProfileStore();
  const [currentNickname, setCurrentNickname] = useState(nickname);
  const [oldPassword, setOldPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');

  useEffect(() => {
    setCurrentNickname(nickname);
  }, [nickname]);

  const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
    setCurrentNickname(event.target.value);
  };

  const handleChangeNickname = () => {
    if (nickname !== currentNickname) {
      changeNickname(currentNickname);
    }
  };

  const submitChangeNickname = (event: FormEvent) => {
    event.preventDefault();
    handleChangeNickname();
    if (ref && ref.current) {
      ref.current.blur();
    }
  };

  const submitPasswordChange = (event: FormEvent) => {
    event.preventDefault();
    handleChangePassword();
  };

  const handleChangePassword = () => {
    changePassword(oldPassword, newPassword, () => {
      setOldPassword('');
      setNewPassword('');
    });
  };

  const isValid = (error: ProfileError | null, key: ProfileErrorKey): boolean => {
    return !error || error.field !== key;
  };

  return (
    <Screen isFetching={isProfileStoreFetching}>
      <Screen.Header backTo={Routes.Home}>
        <Title title="Profile"/>
      </Screen.Header>
      <Screen.Content>
        {!!error && error.field === 'nickname' && (
          <Message type={MessageType.error}>{error.message}</Message>
        )}
        {passwordWasChanged && (
          <Message type={MessageType.warning}>Password was changed successfully!</Message>
        )}
        <div className={styles.profileAvatarContainer}>
          <AvatarIcon
            type={AvatarIconTypes.Round}
            image={avatar}
            additionalClasses={styles.profileAvatar}
          />
          <Button
            title="Change Avatar"
            onClick={() => history.push(Routes.ProfileChangeAvatar)}
            type={ButtonTypes.Dark}
          />
        </div>
        <TextInput
          value={email}
          label="Email"
          name="email"
          disabled
          additionalClasses="verticalSpace"
        />
        <form onSubmit={submitChangeNickname}>
          <TextInput
            value={currentNickname}
            onChange={handleChange}
            onBlur={handleChangeNickname}
            inputRef={ref}
            label="Username"
            name="nickname"
            isValid={isValid(error, 'nickname')}
            additionalClasses="verticalSpace"
          />
        </form>
        <Accordion>
          <Accordion.Panel title="Change password" icon={<LockIcon className={styles.passwordIcon}/>} key={0}>
            {!!error && (error.field === 'oldPassword' || error.field === 'newPassword') && (
              <Message type={MessageType.error}>{error.message}</Message>
            )}
            <form onSubmit={submitPasswordChange}>
              <TextInput
                value={oldPassword}
                onChange={(event) => setOldPassword(event.target.value)}
                type="password"
                label="Old Password"
                name="oldPassword"
                isValid={isValid(error, 'oldPassword')}
                additionalClasses="verticalSpace"
              />
              <TextInput
                value={newPassword}
                onChange={(event) => setNewPassword(event.target.value)}
                type="password"
                label="New Password"
                name="newPassword"
                isValid={isValid(error, 'newPassword')}
                additionalClasses="verticalSpace"
              />
              <div className={styles.changeButton}>
                <Button
                  title="Change"
                  onClick={handleChangePassword}
                  type={ButtonTypes.Main}
                />
              </div>
            </form>
          </Accordion.Panel>
        </Accordion>
      </Screen.Content>
    </Screen>
  );
};
