import React, {FC, useEffect, useState} from 'react';
import {useHistory} from 'react-router-dom';
import {Routes} from '../../app/routes';
import {Screen} from '../../common/Screen.component';
import {Title} from '../../common/Title.component';
import {Progress} from '../../common/Progress.component';
import {Team} from '../common/Team.component';
import {useTeamStore} from '../common/Team.store';

export const TeamProfile: FC = () => {
  const history = useHistory();
  const {teamName, changeTeamName, validateTeamName, teamPlayers, isCurrentUserCaptain, error, progress, isTeamStoreFetching} = useTeamStore();
  const [currentTeamName, setCurrentTeamName] = useState('');

  useEffect(() => {
    setCurrentTeamName(teamName);
  }, [teamName]);

  const handleBack = () => {
    validateTeamName(currentTeamName)
      .then(() => {
        history.push(Routes.Home);
      })
      .catch(() => {});
  };

  return (
    <Screen isFetching={isTeamStoreFetching}>
      <Screen.Header onBack={handleBack}>
        <Title title="Team"/>
      </Screen.Header>
      <Screen.Content>
        <Progress progress={progress} additionalClasses="verticalSpace"/>
        <Team
          currentTeamName={currentTeamName}
          changeTeamName={changeTeamName}
          setCurrentTeamName={setCurrentTeamName}
          teamPlayers={teamPlayers}
          isCurrentUserCaptain={isCurrentUserCaptain}
          error={error}
        />
      </Screen.Content>
    </Screen>
  );
};
