import React, {createContext, FC, useState} from 'react';

interface AvatarContextProps {
  readonly addAvatar: (avatarUrl: string, avatarData: string) => void;
  readonly avatars: Map<string, string>;
}

export const AvatarContext = createContext<AvatarContextProps>({
  addAvatar: (avatarUrl: string, avatarData: string) => {},
  avatars: new Map(),
});


export const AvatarContextProvider: FC = ({children}) => {
  const [avatars, setAvatars] = useState<Map<string, string>>(new Map());

  const addAvatar = (avatarUrl: string, avatarData: string) => {
    setAvatars(prev => {
      return new Map(prev.set(avatarUrl, avatarData));
    });
  };

  return (
    <AvatarContext.Provider value={{
      addAvatar,
      avatars,
    }}
    >
      {children}
    </AvatarContext.Provider>
  );
};
