import React, {ChangeEvent, FC, FormEvent, useEffect, useRef, useState} from 'react';
import {Screen} from '../../common/Screen.component';
import {TextInput} from '../../common/TextInput.component';
import {Label} from '../../common/Label.component';
import {Title} from '../../common/Title.component';
import {Button, ButtonTypes} from '../../common/Button.component';
import {Message, MessageType} from '../../common/Message.component';
import {ReactComponent as PersonIcon} from '../../images/person.svg';
import {AvatarSelector} from './AvatarSelector.component';
import {useProfileStore} from './Profile.store';
import {useAvatarListStore} from './AvatarListStore';
import styles from './Personalize.module.scss';

export const Personalize: FC = () => {
  const ref = useRef<HTMLInputElement>(null);
  const {nickname, changeNickname, markProfileCreated, error, isProfileStoreFetching} = useProfileStore();
  const {isAvatarListStoreFetching} = useAvatarListStore();
  const [currentNickname, setCurrentNickname] = useState(nickname);

  useEffect(() => {
    setCurrentNickname(nickname);
  }, [nickname]);

  const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
    setCurrentNickname(event.target.value);
  };

  const handleChangeNickname = () => {
    changeNickname(currentNickname);
  };

  const submitChangeNickname = (event: FormEvent) => {
    event.preventDefault();
    handleChangeNickname();
    if (ref && ref.current) {
      ref.current.blur();
    }
  };

  const handleContinue = () => {
    markProfileCreated();
  };

  return (
    <Screen footerWithGradient isFetching={isProfileStoreFetching || isAvatarListStoreFetching}>
      <Screen.Header>
        <Title title="Personalize" icon={<PersonIcon className={styles.personIcon}/>}/>
      </Screen.Header>
      <Screen.Content>
        {!!error && (
          <Message type={MessageType.error}>{error.message}</Message>
        )}
        <form onSubmit={submitChangeNickname}>
          <TextInput
            value={currentNickname}
            inputRef={ref}
            onChange={handleChange}
            onBlur={handleChangeNickname}
            isValid={error?.field !== 'nickname'}
            label="Username"
            name="nickname"
            additionalClasses="verticalSpace"
          />
        </form>
        <Label label="Avatar"/>
        <AvatarSelector/>
      </Screen.Content>
      <Screen.Footer centered>
        <Button title="Continue" onClick={handleContinue} type={ButtonTypes.Main}/>
      </Screen.Footer>
    </Screen>
  );
};
