import axios from 'axios';
import {FinalEventPage, Story, StoryPage, StoryResponse} from './story.types';

export const getMainStoryRequest = () =>
  axios.get<StoryResponse>('/games/self/main-story')
    .then(({data}) => storyMapper(data))
    .catch(error => {
      throw error.response.data;
    });

export const getDailyStoryRequest = (dayNumber?: number) => {
  if (dayNumber) {
    return axios.get(`/games/self/daily-story/${dayNumber}`)
      .then(({data}) => storyMapper(data))
      .catch(error => {
        throw error.response.data;
      });
  } else {
    return axios.get('/games/self/daily-story')
      .then(({data}) => storyMapper(data))
      .catch(error => {
        throw error.response.data;
      });
  }
};

export const getFinalStoryRequest = () =>
  axios.get('/games/self/final-story')
    .then(({data}) => storyMapper(data))
    .catch(error => {
      throw error.response.data;
    });

export const getFinalEventRequest = () =>
  axios.get('/games/self/final-event')
    .then(({data}) => finalEventMapper(data))
    .catch(error => {
      throw error.response.data;
    });

const storyMapper = (response: StoryResponse): Story => ({
  pages: response.pages.map(({text, resource_url}): StoryPage => ({
    text,
    resource: resource_url,
  })),
});

const finalEventMapper = (data: any): FinalEventPage => ({
  text: data['text'],
  resource: data['resource_url'],
  externalLink: data['external_link_url'],
});

export const mainStoryViewedRequest = () => {
  return axios.patch('/self', {
    key: 'viewed_main_story',
    value_bool: true,
  })
    .catch(error => {
      throw error.response.data;
    });
};

export const dailyStoryViewedRequest = (dayNumber: number) => {
  return axios.patch(`/self/day/${dayNumber}`, {
    key: 'day_story_viewed',
    value_bool: true,
  })
    .catch(error => {
      throw error.response.data;
    });
};

export const finalStoryViewedRequest = () => {
  return axios.patch('/self', {
    key: 'final_story_viewed',
    value_bool: true,
  })
    .catch(error => {
      throw error.response.data;
    });
};
