import React, {FC} from 'react';
import {Screen} from '../../common/Screen.component';
import {Panel} from '../../common/Panel.components';
import {Button, ButtonTypes} from '../../common/Button.component';
import {Title} from '../../common/Title.component';
import {ReactComponent as CompletedIcon} from '../../images/completed.svg';
import {useQuestionnaireStore} from './Questionnaire.store';
import styles from './FinalPage.module.scss';

export const FinalPage: FC = () => {
  const {feedbackForm, markFinalPageViewed, isQuestionnaireStoreFetching} = useQuestionnaireStore();

  return (
    <Screen footerWithActions isFetching={isQuestionnaireStoreFetching}>
      <Screen.Header>
        <Title title="" onClose={markFinalPageViewed}/>
      </Screen.Header>
      <Screen.Content>
        <Panel
          icon={<CompletedIcon/>}
          mainText="Mission completed!"
          additionalText="Today you made a piece of history. Together you are unstoppable and can create legends. Thank you! How was it for you? Fill out the form below to let us know about your experience with the quest."
        />
      </Screen.Content>
      <Screen.Footer>
        <Button
          title="Rate experience"
          type={ButtonTypes.Secondary}
          additionalClasses={styles.rateButton}
          onClick={() => {
            window.open(feedbackForm);
          }}
        />
      </Screen.Footer>
    </Screen>
  );
};
