import React, {FC} from 'react';
import classNames from 'classnames';
import {Button, ButtonTypes} from './Button.component';
import styles from './CarouselControls.module.scss';

interface CarouselControlsProps<Type = any> {
  readonly items: Array<Type>;
  readonly activeIndex: number;
  readonly setActiveIndex: (index: number) => void;
  readonly onLastItem: () => void;
  readonly nextItemButtonTitle: string;
  readonly lastItemButtonTitle: string;
}

export const CarouselControls: FC<CarouselControlsProps> = ({
  items,
  activeIndex,
  setActiveIndex,
  onLastItem,
  nextItemButtonTitle,
  lastItemButtonTitle,
}) => {
  return (
    <div className={styles.carouselControls}>
      <div className={styles.carouselDots}>
        {items.length > 1 &&
        items.map((item, index) => (
          <div
            key={index}
            className={classNames(styles.carouselDot, {
              [styles.activeDot]: activeIndex === index,
            })}
            onClick={() => setActiveIndex(index)}
          />
        ))}
      </div>
      <div className={styles.carouselButtons}>
        <Button
          title={activeIndex === items.length - 1 ? lastItemButtonTitle : nextItemButtonTitle}
          type={ButtonTypes.Main}
          onClick={onLastItem}
          additionalClasses={styles.carouselButton}
        />
      </div>
    </div>
  );
};
