import React, {FC} from 'react';
import {Routes} from '../../app/routes';
import {Screen} from '../../common/Screen.component';
import {Title} from '../../common/Title.component';
import {Panel} from '../../common/Panel.components';
import {Button, ButtonTypes} from '../../common/Button.component';
import {ReactComponent as ContactIcon} from '../../images/contact.svg';
import {useGameStore} from '../Game.store';
import styles from './Contact.module.scss';

export const Contact: FC = () => {
  const {gameState, isGameStoreFetching} = useGameStore();

  return (
    <Screen footerWithActions isFetching={isGameStoreFetching}>
      <Screen.Header backTo={Routes.Home}>
        <Title title="Contact"/>
      </Screen.Header>
      <Screen.Content>
        <Panel
          icon={<ContactIcon/>}
          mainText="Need help?"
          additionalText="Sherlock got his Watson, you have us - Help Force One! Here to serve you. Need a live hint or somethings gone rogue contact our live top agents @HelpForce1. ps. use contact HQ only when instructed."
        />
      </Screen.Content>
      <Screen.Footer additionalClasses={styles.contactFooter}>
        <Button
          title="Live HelpForce1"
          type={ButtonTypes.Secondary}
          additionalClasses={styles.contactButton}
          onClick={() => {
            window.open(gameState.linkToHelp);
          }}
        />
        <Button
          title="Contact HQ"
          type={ButtonTypes.Main}
          onClick={() => {
            window.open(gameState.linkToBot);
          }}
        />
      </Screen.Footer>
    </Screen>
  );
};
