import React, {FC, MouseEventHandler} from 'react';
import classNames from 'classnames';
import styles from './Button.module.scss';

export enum ButtonTypes {
  Main,
  Secondary,
  Dark,
}

interface ButtonProps {
  readonly title: string;
  readonly onClick?: MouseEventHandler<HTMLButtonElement>;
  readonly type: ButtonTypes;
  readonly additionalClasses?: string;
}

export const Button: FC<ButtonProps> = ({title, onClick, type, additionalClasses}) => {
  return (
    <button
      className={classNames(styles.button, additionalClasses, {
        [styles.main]: type === ButtonTypes.Main,
        [styles.secondary]: type === ButtonTypes.Secondary,
        [styles.dark]: type === ButtonTypes.Dark
      })}
      onClick={onClick}
    >
      {title}
    </button>
  );
};
