import React, {FC} from 'react';
import classNames from 'classnames';
import {useResourceStore} from '../mission/Resource.store';
import styles from './ImageResource.module.scss';

interface ImageResourceProps {
  readonly url: string;
  readonly additionalClasses?: string;
}

export const ImageResource: FC<ImageResourceProps> = ({url, additionalClasses}) => {
  const {isFetching, resourceUrl} = useResourceStore(url);

  if (isFetching || !resourceUrl) {
    return <div className={classNames(styles.placeholder, additionalClasses)}/>;
  }

  return (
    <img src={resourceUrl} className={classNames(styles.image, additionalClasses)} alt=""/>
  );
};
