import React, {FC} from 'react';
import {InventoryItem} from '../../api/game.types';
import {Inventory, InventorySize} from '../common/Inventory.component';
import styles from './DailyInventoryItems.module.scss';

export interface DailyInventoryItemsProps {
  readonly inventoryItems: Array<InventoryItem>;
}

export const DailyInventoryItems: FC<DailyInventoryItemsProps> = ({inventoryItems}) => {
  const availableInventoryItems = inventoryItems.filter(({isAvailable}) => isAvailable);

  if (availableInventoryItems.length === 0) {
    return null;
  }

  return (
    <div className={styles.inventoryItems}>
      {
        availableInventoryItems.map((inventory, index) => (
          <div key={index} className={styles.inventory}>
            <Inventory inventory={inventory} size={InventorySize.Small}/>
            <div className={styles.inventoryInfo}>
              <div className={styles.inventoryTitle}>Personal Item</div>
              <div className={styles.inventoryName}>{inventory.name}</div>
            </div>
          </div>
        ))
      }
    </div>
  );
};
