import React, {ChangeEvent, FC, useCallback, useState} from 'react';
import {Routes} from '../../app/routes';
import {Link} from '../../common/Link.component';
import {Title} from '../../common/Title.component';
import {Button, ButtonTypes} from '../../common/Button.component';
import {Checkbox} from '../../common/Checkbox.component';
import {Screen} from '../../common/Screen.component';
import {ReactComponent as LoginIcon} from '../../images/login-icon.svg';
import {useAuthStore} from './Auth.store';
import {Auth} from './Auth.component';
import styles from './Login.module.scss';

export const Login: FC = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [isRememberChecked, setIsRememberChecked] = useState(false);
  const {login, errors, isAuthStoreFetching} = useAuthStore();

  const handleChangeEmail = (email: string): void => setEmail(email);
  const handleChangePassword = (password: string): void => setPassword(password);

  const handleLogin = useCallback(() => {
    login({
      credential: {email, password},
      isRememberChecked,
    });
  }, [email, isRememberChecked, login, password]);

  return (
    <Screen footerWithGradient isFetching={isAuthStoreFetching}>
      <Screen.Header>
        <Title title="Log In" icon={<LoginIcon/>}/>
      </Screen.Header>
      <Screen.Content>
        <Auth
          errors={errors}
          email={email}
          onEmailChange={handleChangeEmail}
          password={password}
          onPasswordChange={handleChangePassword}
        />
        <Checkbox
          checked={isRememberChecked}
          onChange={(event: ChangeEvent<HTMLInputElement>) => setIsRememberChecked(event.target.checked)}
          name="remember"
          additionalClasses="verticalSpace"
        >
          Remember me on this device
        </Checkbox>
        <p className={styles.text}>
          If you forgot your password please contact admin@questmission.com
        </p>
        <Link to={Routes.Signup}>Don&apos;t have an account yet?</Link>
      </Screen.Content>
      <Screen.Footer centered>
        <Button title="Login" onClick={handleLogin} type={ButtonTypes.Main}/>
      </Screen.Footer>
    </Screen>
  );
};
