import React, {FC, useEffect} from 'react';
import {useLocation} from 'react-router-dom';
import {History} from 'history';
import {Routes, routesToTimeTrack} from './routes';
import {getAccessToken} from '../lib/token';
import {TrackingType, useTimeTrackingStore} from '../pages/common/TimeTracking.store';

interface TimeTrackerProps {
  history: History;
}

export const TimeTracker: FC<TimeTrackerProps> = ({history}) => {
  const {pathname} = useLocation();
  const {isTrackingEnabled, setIsTrackingEnabled} = useTimeTrackingStore(TrackingType.TotalTime);

  useEffect(() => {
    if (!isTrackingEnabled && routesToTimeTrack.includes(pathname as Routes)) {
      setIsTrackingEnabled(!!getAccessToken());
    }

    return history.listen((location) => {
      if (!isTrackingEnabled && routesToTimeTrack.includes(location.pathname as Routes)) {
        setIsTrackingEnabled(!!getAccessToken());
      }
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pathname]);

  return (
    <div style={{display: 'hidden'}}/>
  );
};
