import React, {FC} from 'react';
import {useHistory} from 'react-router-dom';
import {Routes} from '../../app/routes';
import {StoryType, useStoryStore} from '../common/Story.store';
import {useGameStore} from '../Game.store';
import {StoryScreen} from '../common/StoryScreen.component';

export const FinalStory: FC = () => {
  const history = useHistory();
  const {gameState} = useGameStore();
  const {finalStory, markFinalViewed, isStoryStoreFetching} = useStoryStore(StoryType.Final);

  const handleStart = () => {
    markFinalViewed();
  };

  return (
    <StoryScreen
      pages={!finalStory ? [] : finalStory.pages}
      isFetching={isStoryStoreFetching}
      onStart={handleStart}
      onClose={gameState.isFinalStoryViewed ? () => history.push(Routes.Home) : undefined}
    />
  );
};
