import React, {FC, lazy, Suspense} from 'react';
import {useHistory} from 'react-router-dom';
import {Spinner} from '../../common/Spinner.component';
import {ReactComponent as CloseIcon} from '../../images/close.svg';
import styles from './VrTask.module.scss';

const VrScene = lazy(() => import('./VrScene.component'));

export const VrTask: FC = () => {
  const history = useHistory();

  return (
    <div className={styles.vrTaskContainer}>
      <Suspense fallback={<Spinner isLoading={true}/>}>
        <button className={styles.closeButton} onClick={() => history.goBack()}>
          <CloseIcon className={styles.closeButtonIcon}/>
        </button>
        <VrScene/>
      </Suspense>
    </div>
  );
};
