import axios from 'axios';

export const getAvatars = () =>
  axios.get('/resources/avatars')
    .then(({data}) => data.resources)
    .catch(error => {
      throw error.response.data;
    });

export const getAvatarRequest = (avatar: string) =>
  axios.get(`/resources/avatars/${filenameFromUrl(avatar)}`, {responseType: 'blob'})
    .then(({data}) => data)
    .catch(error => {
      throw error.response.data;
    });

export const getImageRequest = (imageUrl: string) =>
  axios.get(`/resources/images/${filenameFromUrl(imageUrl)}`, {responseType: 'blob'})
  // axios.get(imageUrl, {responseType: 'blob'})
    .then(({data}) => data)
    .catch(error => {
      throw error.response.data;
    });

const filenameFromUrl = (url: string): string | undefined => url.split('/').pop();
