import {useMutation, useQuery, useQueryClient} from 'react-query';
import {useHistory} from 'react-router-dom';
import {Routes} from '../../app/routes';
import {FinalEventPage, Story} from '../../api/story.types';
import {DayStatus} from '../../api/game.types';
import {
  dailyStoryViewedRequest,
  finalStoryViewedRequest,
  getDailyStoryRequest,
  getFinalStoryRequest,
  getFinalEventRequest,
  getMainStoryRequest,
  mainStoryViewedRequest
} from '../../api/Story.api';
import {useGameStore} from '../Game.store';

export enum StoryType {
  Main,
  Daily,
  Final,
  FinalEvent,
}

interface StoryStore {
  readonly isStoryStoreFetching: boolean;
  readonly mainStory: Story | null;
  readonly dailyStory: Story | null;
  readonly finalStory: Story | null;
  readonly finalEvent: FinalEventPage | null;
  readonly markMainDailyStoryViewed: () => void;
  readonly markDailyStoryViewed: () => void;
  readonly markFinalViewed: () => void;
}

export const useStoryStore = (type: StoryType, dayNumber?: number): StoryStore => {
  const queryClient = useQueryClient();
  const history = useHistory();
  const {currentDay} = useGameStore();
  const {
    data: mainStory,
    isFetching: isMainStoryFetching,
  } = useQuery<Story>('main-story', getMainStoryRequest, {
    staleTime: Infinity,
    enabled: type === StoryType.Main,
  });
  const {
    data: dailyStory,
    isFetching: isDailyStoryFetching,
  } = useQuery<Story>(
    ['daily-story', dayNumber],
    () => getDailyStoryRequest(dayNumber),
    {
      staleTime: Infinity,
      enabled: type === StoryType.Daily,
    });
  const {
    data: finalStory,
    isFetching: isFinalStoryFetching,
  } = useQuery<Story>('final-story', getFinalStoryRequest, {
    staleTime: Infinity,
    enabled: type === StoryType.Final,
  });
  const {
    data: finalEvent,
    isFetching: isFinalEventFetching,
  } = useQuery<FinalEventPage>('final-event', getFinalEventRequest, {
    staleTime: Infinity,
    enabled: type === StoryType.FinalEvent,
  });
  const markMainStoryViewedMutation = useMutation(mainStoryViewedRequest, {
    onSuccess: () => {
      queryClient.invalidateQueries('game');
      history.push(Routes.Home);
    },
  });
  const markDailyStoryViewedMutation = useMutation(dailyStoryViewedRequest, {
    onSuccess: () => {
      queryClient.invalidateQueries('game');
      queryClient.invalidateQueries(['daily-story', currentDay.dayNumber]);
      history.push(Routes.Mission);
    },
  });
  const markFinalStoryViewedMutation = useMutation(finalStoryViewedRequest, {
    onSuccess: () => {
      queryClient.invalidateQueries('game');
      queryClient.invalidateQueries('final-story');
      history.push(Routes.Home);
    },
  });

  const markMainDailyStoryViewed = (): void => {
    markMainStoryViewedMutation.mutate();
  };

  const markDailyStoryViewed = (): void => {
    if (currentDay.dayNumber === dayNumber && currentDay.status !== DayStatus.Solved) {
      markDailyStoryViewedMutation.mutate(currentDay.dayNumber);
    } else {
      history.push(Routes.Home);
    }
  };

  const markFinalViewed = (): void => {
    markFinalStoryViewedMutation.mutate();
  };

  return {
    isStoryStoreFetching: isMainStoryFetching || isDailyStoryFetching || isFinalStoryFetching || isFinalEventFetching
      || markMainStoryViewedMutation.isLoading || markDailyStoryViewedMutation.isLoading
      || markFinalStoryViewedMutation.isLoading,
    mainStory: mainStory || null,
    dailyStory: dailyStory || null,
    finalStory: finalStory || null,
    finalEvent: finalEvent || null,
    markMainDailyStoryViewed,
    markDailyStoryViewed,
    markFinalViewed,
  };
};
