import React, {FC} from 'react';
import {useHistory, useLocation} from 'react-router-dom';
import {Routes} from '../../app/routes';
import {StoryType, useStoryStore} from '../common/Story.store';
import {StoryScreen} from '../common/StoryScreen.component';

export const MissionStory: FC = () => {
  const history = useHistory();
  const location = useLocation<{dayNumber: number}>();
  const {dailyStory, markDailyStoryViewed, isStoryStoreFetching} = useStoryStore(StoryType.Daily, location.state.dayNumber);

  const handleStart = () => {
    markDailyStoryViewed();
  };

  return (
    <StoryScreen
      pages={!dailyStory ? [] : dailyStory.pages}
      isFetching={isStoryStoreFetching}
      onStart={handleStart}
      onClose={() => {history.push(Routes.Home);}}
    />
  );
};
