import React, {FC} from 'react';
import {Screen} from '../../common/Screen.component';
import {Title} from '../../common/Title.component';
import {Routes} from '../../app/routes';
import {AvatarSelector} from '../onboarding/AvatarSelector.component';
import {useAvatarListStore} from '../onboarding/AvatarListStore';

export const ChangeAvatar: FC = () => {
  const {isAvatarListStoreFetching} = useAvatarListStore();
  return (
    <Screen isFetching={isAvatarListStoreFetching}>
      <Screen.Header backTo={Routes.Profile}>
        <Title title="Change Avatar"/>
      </Screen.Header>
      <Screen.Content>
        <AvatarSelector/>
      </Screen.Content>
    </Screen>
  );
};
