import React from 'react';
import {Routes} from '../../app/routes';
import {StoryType, useStoryStore} from '../common/Story.store';
import {Screen} from '../../common/Screen.component';
import {Title} from '../../common/Title.component';
import {ImageResource} from '../common/ImageResource.component';
import {Button, ButtonTypes} from '../../common/Button.component';

export const FinalEvent = () => {
  const {finalEvent, isStoryStoreFetching} = useStoryStore(StoryType.FinalEvent);

  if (!finalEvent) {
    return null;
  }

  return (
    <Screen isFetching={isStoryStoreFetching}>
      <Screen.Header backTo={Routes.Home}>
        <Title title="Final Mission"/>
      </Screen.Header>
      <Screen.Content>
        <div className="verticalSpace">
          {finalEvent.text}
        </div>
        <ImageResource url={finalEvent.resource} additionalClasses="verticalSpace"/>
        <div className="centerContent">
          <Button
            title="Go to details"
            type={ButtonTypes.Main}
            onClick={() => window.open(finalEvent?.externalLink)}
          />
        </div>
      </Screen.Content>
    </Screen>
  );
};
