import React, {FC} from 'react';
import classNames from 'classnames';
import {AvatarIcon, AvatarIconTypes} from '../../common/AvatarIcon.component';
import {useAvatarStore} from '../onboarding/Avatar.store';
import styles from './Avatar.module.scss';

interface AvatarProps {
  readonly avatarUrl: string;
  readonly type: AvatarIconTypes;
  readonly isSelected?: boolean;
  readonly onClick?: () => void;
}

export const Avatar: FC<AvatarProps> = ({avatarUrl, type, isSelected = false, onClick}) => {
  const {isAvatarStoreFetching, avatar} = useAvatarStore(avatarUrl);

  return (
    <AvatarIcon
      image={avatar}
      onClick={onClick}
      type={type}
      additionalClasses={classNames({
        [styles.currentAvatar]: !isAvatarStoreFetching && isSelected,
      })}
    />
  );
};
