import axios from 'axios';
import {TeamInfo, Team, TeamPatchModel, TeamPlayer, DayInfo} from './team.types';

export const getTeamInfoRequest = () =>
  axios.get('/teams/self')
    .then(({data}) => teamInfoMapper(data))
    .catch(error => {
      throw error.response.data;
    });

const teamInfoMapper = (teamInfoData: any): TeamInfo => {
  return {
    name: teamInfoData['name'],
    days: !teamInfoData['days'] ? [] : teamInfoData['days'].map(
      (dayInfo: any): DayInfo => ({
        isTeamMissionSolved: dayInfo['daily_mission_solved'],
        totalTasks: dayInfo['total_tasks'],
        totalTasksSolved: dayInfo['total_tasks_solved'],
      })
    ),
  };
};

export const getTeamRequest = () =>
  axios.get('/teams/self/players')
    .then(({data}) => teamMapper(data))
    .catch(error => {
      throw error.response.data;
    });

const teamMapper = (teamData: any): Team => {
  const captain = teamData['capitan'];
  const captainId = !captain ? null : captain['public-id'];
  return {
    captainId,
    players: teamData.players.map((player: any): TeamPlayer => ({
      id: player['public-id'],
      isCaptain: player['public-id'] === captainId,
      nickname: player['nickname'],
      avatarUrl: player['avatar'],
      email: player['email'],
      chatUrl: player['chat_url'],
    }))
  };
};

export const patchTeamRequest = (patchModel: TeamPatchModel) => {
  return axios.patch('/teams/self', {
    key: patchModel.key,
    'value_str': patchModel.value,
  })
    .catch(error => {
      throw error.response.data;
    });
};
