import Cookies from 'js-cookie';
import CryptoJS from 'crypto-js';
import {AuthModel} from '../api/auth.types';

const ACCESS_TOKEN_KEY = 'access_token';
const REFRESH_TOKEN_KEY = 'refresh_token';
const USER_ID_KEY = 'user_id';
const DAYS_TO_STORE_TOKEN = 5;

export const storeTokens = (authInfo: AuthModel, shouldStore: boolean): void => {
  const encryptedAccessToken = CryptoJS.AES.encrypt(authInfo.accessToken, ACCESS_TOKEN_KEY).toString();
  const encryptedRefreshToken = CryptoJS.AES.encrypt(authInfo.refreshToken, REFRESH_TOKEN_KEY).toString();
  if (shouldStore) {
    Cookies.set(
      ACCESS_TOKEN_KEY, encryptedAccessToken,
      {expires: DAYS_TO_STORE_TOKEN}
    );
    Cookies.set(
      REFRESH_TOKEN_KEY, encryptedRefreshToken,
      {expires: DAYS_TO_STORE_TOKEN}
    );
    try {
      if (authInfo.userId) {
        window.localStorage.setItem(USER_ID_KEY, authInfo.userId);
      }
    } catch (error) {/* no op */
    }
  } else {
    try {
      window.sessionStorage.setItem(ACCESS_TOKEN_KEY, encryptedAccessToken);
      window.sessionStorage.setItem(REFRESH_TOKEN_KEY, encryptedRefreshToken);
      if (authInfo.userId) {
        window.sessionStorage.setItem(USER_ID_KEY, authInfo.userId);
      }
    } catch (error) {/* no op */
    }
  }
};

export const getAccessToken = (): string | null => {
  let token: string | undefined | null = null;

  try {
    token = Cookies.get(ACCESS_TOKEN_KEY);

    if (!token) {
      token = window.sessionStorage.getItem(ACCESS_TOKEN_KEY);
    }
  } catch (error) {/* no op */
  }

  return token ? CryptoJS.AES.decrypt(token, ACCESS_TOKEN_KEY).toString(CryptoJS.enc.Utf8) : null;
};

export const getRefreshToken = (): string | null => {
  let token: string | undefined | null = null;

  try {
    token = Cookies.get(REFRESH_TOKEN_KEY);

    if (!token) {
      token = window.sessionStorage.getItem(REFRESH_TOKEN_KEY);
    }
  } catch (error) {/* no op */
  }

  return token ? CryptoJS.AES.decrypt(token, REFRESH_TOKEN_KEY).toString(CryptoJS.enc.Utf8) : null;
};

export const getUserId = (): string | null => {
  let id = window.localStorage.getItem(USER_ID_KEY);
  if (!id) {
    id = window.sessionStorage.getItem(USER_ID_KEY);
  }

  return id;
};

export const isTokensStoredInCookies = (): boolean => {
  let token: string | undefined | null = null;

  try {
    token = Cookies.get(ACCESS_TOKEN_KEY);
  } catch (error) {/* no op */
  }

  return !!token;
};

export const clearTokens = (): void => {
  window.sessionStorage.removeItem(ACCESS_TOKEN_KEY);
  window.sessionStorage.removeItem(REFRESH_TOKEN_KEY);
  Cookies.remove(ACCESS_TOKEN_KEY);
  Cookies.remove(REFRESH_TOKEN_KEY);
};
