import React, {FC} from 'react';
import classNames from 'classnames';
import styles from './Spinner.module.scss';

interface SpinnerProps {
  isLoading: boolean;
}

export const Spinner: FC<SpinnerProps> = ({isLoading}) => {
  return (
    <div className={classNames(
      styles.spinner, {
        [styles.hiddenSpinner]: !isLoading,
      })}
    >
      <div className={styles.rings}>
        <div className={classNames(styles.ring, styles.ring1)}/>
        <div className={classNames(styles.ring, styles.ring2)}/>
        <div className={classNames(styles.ring, styles.ring3)}/>
        <div className={classNames(styles.ring, styles.ring4)}/>
      </div>
    </div>
  );
};
