import axios from 'axios';
import {AuthErrorModel, AuthModel, Credential} from './auth.types';

export const loginRequest = (credential: Credential) =>
  axios.get('/login', {
    headers: {
      'Authorization': `Basic ${btoa(`${credential.email.trim()}:${credential.password}`)}`
    }
  })
    .then(({data}) => authModelMapper(data))
    .catch(error => {
      throw authErrorMapper(error.response.data);
    });

export const signupRequest = (credential: Credential) =>
  axios.get('/signup', {
    headers: {
      'Authorization': `Basic ${btoa(`${credential.email.trim()}:${credential.password.trim()}`)}`
    }
  })
    .then(({data}) => authModelMapper(data))
    .catch(error => {
      throw authErrorMapper(error.response.data);
    });

export const refreshTokenRequest = (accessToken: string | null, refreshToken: string | null) =>
  axios.get('token-refresh', {
    headers: {
      'x-access-token': accessToken,
      'x-refresh-token': refreshToken,
    },
  })
    .then(({data}) => authModelMapper(data));

export const authModelMapper = (data: any): AuthModel => ({
  accessToken: data['access-token'],
  refreshToken: data['refresh-token'],
  userId: data['public-id'],
});

const authErrorMapper = (data: any): AuthErrorModel => ({
  message: data['message'],
  errorCode: data['error_code'],
});
