import axios from 'axios';

export const TRACKING_TIME_AMOUNT_SECONDS = 3;

export const logTotalSpentTimeRequest = () => {
  return axios.post('/self/log-spent-time', {
    object: 'total',
    amount_s: TRACKING_TIME_AMOUNT_SECONDS,
  })
    .catch(error => {
      throw error.response.data;
    });
};

export const logTaskSpentTimeRequest = (taskId?: number) => {
  return axios.post('/self/log-spent-time', {
    object: 'task',
    task_id: taskId,
    amount_s: TRACKING_TIME_AMOUNT_SECONDS,
  })
    .catch(error => {
      throw error.response.data;
    });
};
