import {useMutation, useQuery, useQueryClient} from 'react-query';
import {useHistory} from 'react-router-dom';
import {Routes} from '../../app/routes';
import {finalPageViewedRequest, finalQuestionnaireRequest} from '../../api/Game.api';

interface QuestionnaireStore {
  readonly isQuestionnaireStoreFetching: boolean;
  readonly feedbackForm: string;
  readonly markFinalPageViewed: () => void;
}

export const useQuestionnaireStore = (): QuestionnaireStore => {
  const history = useHistory();
  const queryClient = useQueryClient();
  const {data, isFetching} = useQuery<string>('final-questionnaire', finalQuestionnaireRequest, {
    staleTime: Infinity,
  });
  const markFinalPageViewedMutation = useMutation(finalPageViewedRequest, {
    onSuccess: () => {
      queryClient.invalidateQueries('game');
      history.push(Routes.Home);
    },
  });

  const markFinalPageViewed = () => {
    markFinalPageViewedMutation.mutate();
  };

  return {
    isQuestionnaireStoreFetching: isFetching,
    feedbackForm: data || '',
    markFinalPageViewed
  };
};
