import React from 'react';
import {Screen} from '../../common/Screen.component';
import {Panel} from '../../common/Panel.components';
import {ReactComponent as CompletedIcon} from '../../images/completed.svg';
import {Button, ButtonTypes} from '../../common/Button.component';
import {useMissionStore} from '../home/Mission.store';

export const MissionCompleted = () => {
  const {markTeamTaskCompletionViewed, isMissionStoreFetching} = useMissionStore();

  const handleContinue = () => {
    markTeamTaskCompletionViewed();
  };

  return (
    <Screen footerWithGradient isFetching={isMissionStoreFetching}>
      <Screen.Content>
        <Panel
          icon={<CompletedIcon/>}
          mainText="Daily mission completed!"
        />
      </Screen.Content>
      <Screen.Footer centered>
        <Button
          title="Continue"
          onClick={handleContinue}
          type={ButtonTypes.Main}
        />
      </Screen.Footer>
    </Screen>
  );
};

