import {useState} from 'react';
import {useMutation} from 'react-query';
import {useHistory} from 'react-router-dom';
import {storeTokens} from '../../lib/token';
import {storeSignupSuccess} from '../../lib/storage';
import {Routes} from '../../app/routes';
import {loginRequest, signupRequest} from '../../api/Auth.api';
import {AuthError, AuthErrorModel, AuthModel, Credential, LoginInfo, SignupInfo} from '../../api/auth.types';

interface AuthStore {
  readonly login: (loginInfo: LoginInfo) => void;
  readonly signup: (signupInfo: SignupInfo) => void;
  readonly errors: Array<AuthError>;
  readonly isAuthStoreFetching: boolean;
}

export const useAuthStore = (): AuthStore => {
  const history = useHistory();
  const [errors, setErrors] = useState<Array<AuthError>>([]);

  const loginMutation = useMutation<AuthModel, AuthErrorModel, Credential>(loginRequest, {
    onError: error => {
      setErrors([getErrorFromErrorCode(error.errorCode)]);
    },
  });

  const signupMutation = useMutation<AuthModel, AuthErrorModel, Credential>(signupRequest, {
    onError: error => {
      setErrors([getErrorFromErrorCode(error.errorCode)]);
    },
  });

  const tryLogin = (loginInfo: LoginInfo): void => {
    setErrors([]);
    const validationErrors = validateCredential(loginInfo.credential);
    if (validationErrors.length === 0) {
      loginMutation.mutate(loginInfo.credential, {
        onSuccess: (loginData) => {
          storeTokens(loginData, loginInfo.isRememberChecked);
          history.push(Routes.Game);
        }
      });
    } else {
      setErrors(validationErrors);
    }
  };

  const trySignup = (signupInfo: SignupInfo): void => {
    setErrors([]);
    const validationErrors = validateCredential(signupInfo.credential);

    if (!signupInfo.isPolicyChecked) {
      validationErrors.push({field: 'policy', message: 'Read and agree with privacy policy before sign in'});
    }

    if (validationErrors.length === 0) {
      signupMutation.mutate(signupInfo.credential, {
        onSuccess: (loginData) => {
          storeTokens(loginData, signupInfo.isRememberChecked);
          storeSignupSuccess();
          history.push(Routes.Game);
        },
      });
    } else {
      setErrors(validationErrors);
    }
  };

  return {
    isAuthStoreFetching: loginMutation.isLoading || signupMutation.isLoading,
    login: tryLogin,
    signup: trySignup,
    errors,
  };
};

const validateCredential = (credential: Credential): Array<AuthError> => {
  const errors: Array<AuthError> = [];

  const email = credential.email.trim();
  if (email.length === 0) {
    errors.push({field: 'email', message: 'Please enter email'});
  } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
    errors.push({field: 'email', message: 'Please enter a valid email address'});
  }

  const password = credential.password.trim();
  if (password.length === 0) {
    errors.push({
      field: 'password',
      message: 'Please enter password'
    });
  } else if (password.length < 8) {
    errors.push({
      field: 'password',
      message: 'Password must contain at least 8 characters.'
    });
  }

  return errors;
};

const getErrorFromErrorCode = (errorCode: number): AuthError => {
  switch (errorCode) {
    case 2:
      return {
        field: 'email',
        message: 'Your email is not included in the list of participants. If you want to learn more about the game and how to participate, please visit https://promo.page',
      };
    case 3:
      return {
        field: 'email',
        message: 'Please sing up',
      };
    case 4:
      return {
        field: 'password',
        message: 'Incorrect password',
      };
    case 5:
      return {
        field: 'email',
        message: 'Already signed up. Please login',
      };
    default:
      return {
        field: 'email',
        message: 'Something wrong',
      };
  }
};
