import React, {FC, useState} from 'react';
import {useHistory, useLocation} from 'react-router-dom';
import {Routes} from '../../app/routes';
import {InventoryItem} from '../../api/game.types';
import {Screen} from '../../common/Screen.component';
import {Button, ButtonTypes} from '../../common/Button.component';
import {CarouselControls} from '../../common/CarouselControls.component';
import {ImageResource} from '../common/ImageResource.component';
import styles from './ReceivedInventories.module.scss';

export const ReceivedInventories: FC = () => {
  const history = useHistory();
  const [active, setActive] = useState(0);
  const location = useLocation<{ inventories: Array<InventoryItem> }>();
  const inventories = location.state.inventories;

  const handleNext = () => {
    if (active < inventories.length - 1) {
      setActive(prev => prev + 1);
    } else {
      history.push(Routes.Mission);
    }
  };

  return (
    <Screen withoutHeader footerWithGradient={inventories.length === 1}>
      <Screen.Content>
        <div className={styles.title}>
          Congrats!
        </div>
        <div className={styles.message}>
          You have just unlocked the secret to the universe. That or just an item. Either way, keep it handy, useful it
          will be.
        </div>
        <ImageResource url={inventories[active].resource} additionalClasses="verticalSpace"/>
        {inventories[active].name && <div>{inventories[active].name}</div>}
      </Screen.Content>
      <Screen.Footer centered>
        {inventories.length > 1
          ? (
            <CarouselControls
              items={inventories}
              activeIndex={active}
              setActiveIndex={setActive}
              onLastItem={handleNext}
              nextItemButtonTitle="Next"
              lastItemButtonTitle="Continue"
            />)
          : (
            <Button
              title="Continue"
              type={ButtonTypes.Main}
              onClick={handleNext}
            />
          )}
      </Screen.Footer>
    </Screen>
  );
};
