import React, {FC} from 'react';
import {useHistory} from 'react-router-dom';
import dayjs from 'dayjs';
import {Routes} from '../../app/routes';
import {DayStatus} from '../../api/game.types';
import {Screen} from '../../common/Screen.component';
import {Title} from '../../common/Title.component';
import {Button, ButtonTypes} from '../../common/Button.component';
import {AvatarIcon, AvatarIconTypes} from '../../common/AvatarIcon.component';
import {Accordion, AccordionPanelTypes} from '../../common/Accordion.component';
import {InventoryList} from './InventoryList.component';
import {GameInfo} from './GameInfo.components';
import {useProfileStore} from '../onboarding/Profile.store';
import {useGameStore} from '../Game.store';
import {useMissionStore} from './Mission.store';
import {Day} from './Day.component';
import styles from './Home.module.scss';

export const Home: FC = () => {
  const history = useHistory();
  const {avatar, isProfileStoreFetching} = useProfileStore();
  const {gameState, currentDay, areAllTasksSolved, allInventoryItems, isGameStoreFetching} = useGameStore();
  const {isMissionStoreFetching} = useMissionStore();
  const {gameName, days} = gameState;
  const isFetching = isGameStoreFetching || isProfileStoreFetching || isMissionStoreFetching;

  if (!isFetching && currentDay.status === DayStatus.Solved && !currentDay.isTeamTaskCompletionViewed) {
    history.push(Routes.MissionCompleted);
    return null;
  }

  if (!isFetching && areAllTasksSolved) {
    if (!gameState.isFinalStoryViewed) {
      history.push(Routes.FinalStory);
      return null;
    }

    // Temporarily hide Final Page
    // if (!gameState.isFinalPageViewed) {
    //   history.push(Routes.FinalPage);
    //   return null;
    // }
  }

  return (
    <Screen isFetching={isFetching}>
      <Screen.Header>
        <div className={styles.header}>
          <Title title={gameName} additionalClasses={styles.gameName}/>
          <div className={styles.headerActions}>
            <Button
              title="Help"
              onClick={() => history.push(Routes.Contact)}
              type={ButtonTypes.Dark}
              additionalClasses={styles.helpButton}
            />
            <AvatarIcon
              type={AvatarIconTypes.Round}
              onClick={() => history.push(Routes.Profile)}
              image={avatar}
            />
          </div>
        </div>
      </Screen.Header>
      <Screen.Content>
        <InventoryList inventoryItems={allInventoryItems}/>
        <Accordion>
          <Accordion.Panel title="Check In" key={0} checked={gameState.isBotConnected}>
            <div className="centerContent">
              <Button
                title="Continue"
                onClick={() => history.push(Routes.OnboardingCheckIn)}
                type={ButtonTypes.Main}
              />
            </div>
          </Accordion.Panel>
          <Accordion.Panel title="Learn the story" key={1} checked={gameState.isMainStoryViewed}>
            <div className="centerContent">
              <Button
                title="Continue"
                onClick={() => history.push(Routes.OnboardingMainStory)}
                type={ButtonTypes.Main}
              />
            </div>
          </Accordion.Panel>
          {
            days
              .filter(day => day.status !== DayStatus.Future)
              .map((day, index) => {
                return (
                  <Accordion.Panel
                    title={`Mission ${index + 1}`}
                    key={index}
                    status={getTextFromStatus(day.status)}
                    type={
                      (day.status === DayStatus.InProgress || day.status === DayStatus.NotStarted)
                      ? AccordionPanelTypes.Mission
                      : AccordionPanelTypes.Default
                    }
                    isExpandedByDefault={day.status === DayStatus.InProgress || day.status === DayStatus.NotStarted}
                    checked={day.status === DayStatus.Solved}
                  >
                    <Day day={day}/>
                  </Accordion.Panel>
                );
              })
          }
          {areAllTasksSolved && (
            <>
              <Accordion.Panel title="Learn the final story" checked={gameState.isFinalStoryViewed}>
                <div className="centerContent">
                  <Button
                    title="Continue"
                    onClick={() => history.push(Routes.FinalStory)}
                    type={ButtonTypes.Main}
                  />
                </div>
              </Accordion.Panel>
              <Accordion.Panel
                title="Final Mission"
                checked={false}
                isExpandedByDefault
                type={AccordionPanelTypes.Mission}
              >
                <div className="centerContent">
                  {dayjs() < gameState.finalEventStartTime
                    ? (
                      <Button
                        title={`Starts at ${gameState.finalEventStartTime.format('lll')}`}
                        type={ButtonTypes.Secondary}
                      />
                    )
                    : (
                      <Button
                        title="Enter backdoor"
                        onClick={() => history.push(Routes.FinalEvent)}
                        type={ButtonTypes.Main}
                      />
                    )}
                </div>
              </Accordion.Panel>
            </>
          )}
        </Accordion>
      </Screen.Content>
      <Screen.Footer>
        <GameInfo/>
      </Screen.Footer>
    </Screen>
  );
};

const getTextFromStatus = (dayStatus: DayStatus) => {
  switch (dayStatus) {
    case DayStatus.Solved:
      return 'Completed';
    case DayStatus.InProgress:
      return 'In progress';
    default:
      return;
  }
};
