import React, {FC} from 'react';
import classNames from 'classnames';
import styles from './Progress.module.scss';

interface ProgressProps {
  readonly progress: number;
  readonly additionalClasses?: string;
}

export const Progress: FC<ProgressProps> = ({progress, additionalClasses}) => {
  return (
    <div className={classNames(styles.progressContainer, additionalClasses)}>
      <div style={{'width': `${(alignedProgress(progress))}%`}} className={styles.progress}/>
    </div>
  );
};

const alignedProgress = (progress: number): number => {
  if (progress < 0) {
    return 0;
  }

  if (100 < progress) {
    return 100;
  }

  return progress;
};
