import axios from 'axios';
import {History} from 'history';
import {clearTokens, getAccessToken, getRefreshToken, isTokensStoredInCookies, storeTokens} from '../lib/token';
import {refreshTokenRequest} from '../api/Auth.api';
import {Routes} from './routes';

export const setupInterceptors = (history: History) => {
  axios.interceptors.request.use(requestConfig => {
    if (requestConfig.url?.includes('wikiart')) {
      return requestConfig;
    }
    const token = getAccessToken();
    if (token) {
      requestConfig.headers = {
        ...requestConfig.headers,
        'x-access-token': token,
      };
    }
    return requestConfig;
  });

  axios.interceptors.response.use(
    response => response,
    error => {
      if (error.response.status === 401) {
        const originalRequest = error.config;
        refreshTokenRequest(getAccessToken(), getRefreshToken())
          .then(tokens => {
            const shouldStoreTokens = isTokensStoredInCookies();
            clearTokens();
            storeTokens(tokens, shouldStoreTokens);
            return axios(originalRequest);
          })
          .catch(error => {
            clearTokens();
            history.push(Routes.Login);
            return Promise.reject(error);
          });
      }
      return Promise.reject(error);
    },
  );
};
