import {useQuery} from 'react-query';
import dayjs from 'dayjs';
import {selfRequest} from '../api/Game.api';
import {DayInfo, DayStatus, GameState, InventoryItem} from '../api/game.types';

interface GameStore {
  readonly isGameStoreFetching: boolean;
  readonly gameState: GameState;
  readonly currentDay: DayInfo;
  readonly areAllTasksSolved: boolean;
  readonly allInventoryItems: Array<InventoryItem>;
}

const initialGameState: GameState = {
  gameName: '',
  avatarUrl: '',
  isProfileCreated: false,
  isBotConnected: false,
  isMainStoryViewed: false,
  isFinalStoryViewed: false,
  isFinalPageViewed: false,
  nickname: '',
  email: '',
  isNickNameSet: false,
  linkToBot: '',
  linkToHelp: '',
  days: [],
  currentDayNumber: 0,
  finalEventStartTime: dayjs(),
};

const initialDay: DayInfo = {
  isDailyStoryViewed: false,
  isPasscodeEntered: false,
  status: DayStatus.NotStarted,
  startTime: dayjs(),
  dayNumber: 0,
  tasks: [],
  inventoryItems: [],
  totalTasks: 0,
  isTeamTaskSolved: false,
  isTeamTaskCompletionViewed: false,
};

export const useGameStore = (): GameStore => {
  const {data, isFetching} = useQuery<GameState>('game', selfRequest, {
    staleTime: 30 * 1000,
  });

  return {
    isGameStoreFetching: isFetching,
    gameState: data || initialGameState,
    currentDay: !data ? initialDay : data.days[data.currentDayNumber === 0 ? 0 : data.currentDayNumber - 1],
    areAllTasksSolved: !data ? false : data.days.every(({status}) => status === DayStatus.Solved),
    allInventoryItems: !data ? [] : data.days.reduce<Array<InventoryItem>>(
      (acc, dayInfo) => [...acc, ...dayInfo.inventoryItems.filter(({isAvailable}) => isAvailable)],
      []
    ),
  };
};
