import React, {FC} from 'react';
import {useLocation} from 'react-router-dom';
import {Routes} from '../../app/routes';
import {InventoryItem} from '../../api/game.types';
import {Screen} from '../../common/Screen.component';
import {Title} from '../../common/Title.component';
import {ImageResource} from '../common/ImageResource.component';

export const InventoryDetails: FC = () => {
  const location = useLocation<{inventory: InventoryItem}>();
  const inventory = location.state.inventory;

  return (
    <Screen>
      <Screen.Header backTo={Routes.Home}>
        <Title title="Personal Item"/>
      </Screen.Header>
      <Screen.Content>
        <ImageResource url={inventory.resource} additionalClasses="verticalSpace"/>
        <div>{inventory.name}</div>
      </Screen.Content>
    </Screen>
  );
};
