const SIGNUP_SUCCESS_KEY = 'is-registered';

export const storeSignupSuccess = () => {
  try {
    window.localStorage.setItem(SIGNUP_SUCCESS_KEY, 'true');
  } catch (error) {
    // no op
  }
};

export const isSignedUp = () => {
  return window.localStorage.getItem(SIGNUP_SUCCESS_KEY) === 'true';
};
