import {useContext} from 'react';
import {useQuery} from 'react-query';
import {getAvatarRequest} from '../../api/Resources.api';
import {AvatarContext} from '../../contexts/AvatarContext';

export interface AvatarStore {
  readonly isAvatarStoreFetching: boolean;
  readonly avatar?: string;
}

export const useAvatarStore = (avatarUrl: string): AvatarStore => {
  const {addAvatar, avatars} = useContext(AvatarContext);
  const {isFetching} = useQuery(
    ['avatar', avatarUrl],
    () => getAvatarRequest(avatarUrl),
    {
      staleTime: Infinity,
      enabled: !!avatarUrl,
      onSuccess: (data) => {
        const reader = new FileReader();
        reader.readAsDataURL(data);

        reader.onload = function() {
          if (reader.result && typeof reader.result === 'string') {
            addAvatar(avatarUrl, reader.result);
          }
        };
      },
    }
  );

  return {
    isAvatarStoreFetching: isFetching,
    avatar: avatars.get(avatarUrl),
  };
};
