import React, {FC} from 'react';
import {useHistory} from 'react-router-dom';
import {StoryType, useStoryStore} from '../common/Story.store';
import {StoryScreen} from '../common/StoryScreen.component';
import {Routes} from '../../app/routes';

export const MainStory: FC = () => {
  const history = useHistory();
  const {mainStory, markMainDailyStoryViewed, isStoryStoreFetching} = useStoryStore(StoryType.Main);

  const handleStart = () => {
    markMainDailyStoryViewed();
  };

  return (
    <StoryScreen
      pages={!mainStory ? [] : mainStory.pages}
      isFetching={isStoryStoreFetching}
      onStart={handleStart}
      onClose={() => history.push(Routes.Home)}
    />
  );
};
