import React, {FC} from 'react';
import {TaskInfo, TaskType} from '../../api/game.types';
import {Message, MessageType} from '../../common/Message.component';
import {TaskResource} from './TaskResource.component';
import styles from './Task.module.scss';

interface TaskProps {
  readonly task: TaskInfo | null;
}

export const Task: FC<TaskProps> = ({task: originalTask}) => {
  // const task = originalTask;
  const task = {
    ...originalTask,
    type: TaskType.Ar,
    resources: [],
    hints: [],
  };
  return (
    <div>
      {task && !!task.text && (
        <div className={styles.taskText}>
          {task.text}
        </div>
      )}
      {task && <TaskResource type={task.type} resources={task.resources}/>}
      {task && task.isTeamTask && (
        <div className={styles.teamTaskText}>
          1+1+1+...+1 and the job is mostly done. Now, who has one? If only you knew someone...
        </div>
      )}
      {
        task && task.hints.map((hint, index) => (
          <Message key={index} type={MessageType.info}>
            {hint}
          </Message>
        ))
      }
    </div>
  );
};
