import React, {FC} from 'react';
import {AvatarIconTypes} from '../../common/AvatarIcon.component';
import {Avatar} from '../common/Avatar.component';
import {useAvatarListStore} from './AvatarListStore';
import styles from './AvatarSelector.module.scss';

interface AvatarSelectorProps {
}

export const AvatarSelector: FC<AvatarSelectorProps> = () => {
  const {avatarUrlList, currentAvatar, changeAvatar} = useAvatarListStore();

  return (
    <div className={styles.avatarList}>
      {avatarUrlList.map((avatarUrl, index) => (
        <Avatar
          key={index}
          avatarUrl={avatarUrl}
          type={AvatarIconTypes.Square}
          isSelected={currentAvatar === avatarUrl}
          onClick={() => changeAvatar(avatarUrl)}
        />
      ))}
    </div>
  );
};
