import React, {ChangeEvent, FC, FormEvent, useRef} from 'react';
import {TeamPlayer} from '../../api/team.types';
import {TextInput} from '../../common/TextInput.component';
import {AvatarIconTypes} from '../../common/AvatarIcon.component';
import {Message, MessageType} from '../../common/Message.component';
import {TeamError} from './Team.store';
import {Avatar} from './Avatar.component';
import styles from './Team.module.scss';

interface TeamProps {
  readonly currentTeamName: string;
  readonly changeTeamName: (name: string) => void;
  readonly setCurrentTeamName: (name: string) => void;
  readonly teamPlayers: Array<TeamPlayer>;
  readonly isCurrentUserCaptain: boolean;
  readonly error: TeamError | null;
}

export const Team: FC<TeamProps> = ({
  currentTeamName,
  changeTeamName,
  setCurrentTeamName,
  teamPlayers,
  isCurrentUserCaptain,
  error,
}) => {
  const ref = useRef<HTMLInputElement>(null);
  const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
    setCurrentTeamName(event.target.value);
  };

  const handleBlur = () => {
    changeTeamName(currentTeamName);
  };

  const formSubmit = (event: FormEvent) => {
    event.preventDefault();
    changeTeamName(currentTeamName);
    if (ref && ref.current) {
      ref.current.blur();
    }
  };

  return (
    <div>
      {!!error && (
        <Message type={MessageType.error}>{error.message}</Message>
      )}
      <form onSubmit={formSubmit}>
        <TextInput
          inputRef={ref}
          value={currentTeamName}
          onChange={handleChange}
          onBlur={handleBlur}
          disabled={!isCurrentUserCaptain}
          isValid={error?.field !== 'teamName'}
          label="Team Name"
          name="teamName"
          additionalClasses="verticalSpace"
        />
      </form>
      {
        teamPlayers.map(({id, isCaptain, nickname, avatarUrl, email}) => (
          <div key={id} className={styles.player}>
            <Avatar avatarUrl={avatarUrl} type={AvatarIconTypes.Round}/>
            <div className={styles.playerInfo}>
              <div className={styles.nickname}>{`${nickname}${isCaptain ? ' - Captain' : ''}`}</div>
              <div className={styles.email}>{email}</div>
            </div>
          </div>
        ))
      }
    </div>
  );
};
