import React, {FC, useState} from 'react';
import {Screen} from '../../common/Screen.component';
import {Title} from '../../common/Title.component';
import {CarouselControls} from '../../common/CarouselControls.component';
import {ImageResource} from './ImageResource.component';
import {StoryPage} from '../../api/story.types';
import styles from './SroryScreen.module.scss';

interface StoryScreenProps {
  readonly pages: Array<StoryPage>;
  readonly isFetching: boolean;
  readonly onStart: () => void;
  readonly onClose?: () => void;
}

export const StoryScreen: FC<StoryScreenProps> = ({
  pages,
  isFetching,
  onStart,
  onClose,
                                                  }) => {
  const [active, setActive] = useState(0);

  const handleNext = () => {
    if (active < pages.length - 1) {
      setActive(prev => prev + 1);
    } else {
      onStart && onStart();
    }
  };

  return (
    <Screen withoutHeader footerWithGradient isFetching={isFetching}>
      <Screen.Content>
        <Title title="Hello, Agent!" onClose={onClose}/>
        {
          pages.map((page, index) => (
            <StoryPageItem key={index} page={page} hidden={index !== active}/>
          ))
        }
      </Screen.Content>
      <Screen.Footer centered>
        <CarouselControls
          items={pages}
          activeIndex={active}
          setActiveIndex={setActive}
          onLastItem={handleNext}
          nextItemButtonTitle="Next"
          lastItemButtonTitle="Start"
        />
      </Screen.Footer>
    </Screen>
  );
};

interface StoryPageItemProps {
  readonly page: StoryPage;
  readonly hidden: boolean;
}

const StoryPageItem: FC<StoryPageItemProps> = ({page, hidden}) => {
  if (hidden) {
    return null;
  }

  return (
    <>
      {page.resource && <ImageResource url={page.resource}/>}
      <div className={styles.pageText}>
        {page.text}
      </div>
    </>
  );
};
