import React, {FC} from 'react';
import {useHistory} from 'react-router-dom';
import {Routes} from '../../app/routes';
import {TaskType} from '../../api/game.types';
import {Button, ButtonTypes} from '../../common/Button.component';
import {ImageResource} from '../common/ImageResource.component';
import styles from './TaskResource.module.scss';

interface TaskResourceProps {
  readonly type: TaskType;
  readonly resources: Array<string>;
}

export const TaskResource: FC<TaskResourceProps> = ({type, resources}) => {
  const history = useHistory();

  if (type === TaskType.Text) {
    return null;
  }

  if (type === TaskType.Ar) {
    return (
      <div className={styles.urlButton}>
        <Button
          title="Go to AR"
          type={ButtonTypes.Main}
          onClick={() => {
            history.push({
              pathname: Routes.ArTask,
              state: {
                resources,
              }
            });
          }}
        />
      </div>
    );
  }

  if (type === TaskType.Vr) {
    return (
      <>
        {resources[0] && (
          <div className={styles.imageWrapper}>
            <ImageResource url={resources[0]}/>
          </div>
        )}
        <div className={styles.urlButton}>
          <Button
            title="Go to VR"
            type={ButtonTypes.Main}
            onClick={() => {
              history.push({
                pathname: Routes.VrTask,
                state: {
                  resources: resources.slice(1),
                }
              });
            }}
          />
        </div>
      </>
    );
  }

  if (type === TaskType.Url) {
    return (
      <div className={styles.urlButton}>
        <Button
          title="Go to site"
          type={ButtonTypes.Main}
          onClick={() => {
            window.open(resources[0]);
          }}
        />
      </div>
    );
  }

  if (type === TaskType.Video) {
    const videoId = resources[0].split('/').pop();
    return (
      <div className={styles.videoWrapper}>
        <iframe
          src={`https://player.vimeo.com/video/${videoId}?color=ef2200&byline=0`}
          title="video"
          className={styles.videoIframe}
          frameBorder={0}
          allow="fullscreen"
          allowFullScreen
        />
      </div>
    );
  }

  return (
    <div className={styles.imageWrapper}>
      <ImageResource url={resources[0]}/>
    </div>
  );
};


