import React, {FC, useEffect, useState} from 'react';
import {useHistory} from 'react-router-dom';
import {Routes} from '../../app/routes';
import {Screen} from '../../common/Screen.component';
import {Title} from '../../common/Title.component';
import {Button, ButtonTypes} from '../../common/Button.component';
import {Message, MessageType} from '../../common/Message.component';
import {ReactComponent as TeamIcon} from '../../images/team.svg';
import {Team} from '../common/Team.component';
import {useTeamStore} from '../common/Team.store';
import styles from './TeamComposition.module.scss';

export const TeamComposition: FC = () => {
  const history = useHistory();
  const {teamName, changeTeamName, validateTeamName, teamPlayers, isCurrentUserCaptain, error, isTeamStoreFetching} = useTeamStore();
  const [currentTeamName, setCurrentTeamName] = useState('');

  useEffect(() => {
    setCurrentTeamName(teamName);
  }, [teamName]);

  const handleContinue = () => {
    validateTeamName(currentTeamName)
      .then(() => {
        history.push(Routes.OnboardingCheckIn);
      })
      .catch(() => {});
  };

  return (
    <Screen footerWithGradient isFetching={isTeamStoreFetching}>
      <Screen.Header>
        <Title title="Meet the team!" icon={<TeamIcon className={styles.teamIcon}/>}/>
      </Screen.Header>
      <Screen.Content>
        <Message type={MessageType.warning}>
          Welcome, Awesome One. Meet your team of great people. Get in touch with them, via any appropriate means (just
          please do not use smoke signals as they take waaay too long and pollution is real btw!). And, first things
          first: come up with a cool, smart, fun name for your team! Success is created. Together!
        </Message>
        <Team
          currentTeamName={currentTeamName}
          changeTeamName={changeTeamName}
          setCurrentTeamName={setCurrentTeamName}
          teamPlayers={teamPlayers}
          isCurrentUserCaptain={isCurrentUserCaptain}
          error={error}
        />
      </Screen.Content>
      <Screen.Footer centered>
        <Button title="Continue" onClick={handleContinue} type={ButtonTypes.Main}/>
      </Screen.Footer>
    </Screen>
  );
};
