import axios from 'axios';
import {AnswerResponse, AnswerTaskModel, GetHintModel, Hint, SkipTaskModel, UnlockDayModel} from './mission.types';

export const unlockDayRequest = ({dayNumber, answer}: UnlockDayModel) => {
  return axios.patch<{correct: boolean}>(`/self/day/${dayNumber}`, {
    key: 'passcode',
    value_str: answer,
  })
    .catch(error => {
      throw error.response.data;
    });
};

const answerResponseMapper = (data: any): AnswerResponse => ({
  correct: data['correct'],
  inventoryItems: data['new_inventory_items'],
});

export const answerTaskRequest = ({dayNumber, taskId, answer}: AnswerTaskModel) => {
  return axios.patch(`/self/days/${dayNumber}/tasks/${taskId}`, {
    key: 'answer',
    value_str: answer,
  })
    .then(({data}) => answerResponseMapper(data))
    .catch(error => {
      throw error.response.data;
    });
};

export const skipTaskRequest = ({dayNumber, taskId}: SkipTaskModel) => {
  return axios.patch<{skipped: boolean}>(`/self/days/${dayNumber}/tasks/${taskId}`, {
    key: 'skipped',
    value_bool: true,
  })
    .then(({data}) => data)
    .catch(error => {
      throw error.response.data;
    });
};

export const teamTaskCompletionViewedRequest = (dayNumber: number) => {
  return axios.patch(`/self/day/${dayNumber}`, {
    key: 'team_task_completion_viewed',
    value_bool: true,
  })
    .catch(error => {
      throw error.response.data;
    });
};

export const getHintRequest = ({dayNumber, taskId, hintNumber}: GetHintModel) =>
  axios.get<Hint>(`/self/days/${dayNumber}/tasks/${taskId}/hints/${hintNumber}`)
    .then(({data}) => (data))
    .catch(error => {
      throw error.response.data;
    });
