import React, {ChangeEvent, FC, useCallback} from 'react';
import {Message, MessageType} from '../../common/Message.component';
import {TextInput} from '../../common/TextInput.component';
import {AuthError} from '../../api/auth.types';

interface AuthProps {
  readonly errors: Array<AuthError>;
  readonly email: string;
  readonly onEmailChange: (email: string) => void;
  readonly password: string;
  readonly onPasswordChange: (password: string) => void;
}

export const Auth: FC<AuthProps> = ({
  errors,
  email,
  onEmailChange,
  password,
  onPasswordChange,
}) => {
  const handleChangeEmail = useCallback((event: ChangeEvent<HTMLInputElement>) => {
    onEmailChange(event.target.value);
  }, [onEmailChange]);

  const handleChangePassword = useCallback((event: ChangeEvent<HTMLInputElement>) => {
    onPasswordChange(event.target.value);
  }, [onPasswordChange]);

  return <>
    {errors.length > 0 && (
      <Message type={MessageType.error}>
        {errors.map(({message}, index) => <span key={index}>{message}</span>)}
      </Message>
    )}
    <TextInput
      value={email}
      onChange={handleChangeEmail}
      label="email"
      name="email"
      isValid={!errors.find(({field}) => field === 'email')}
      additionalClasses="verticalSpace"
    />
    <TextInput
      value={password}
      onChange={handleChangePassword}
      type="password"
      label="password"
      name="password"
      isValid={!errors.find(({field}) => field === 'password')}
      additionalClasses="verticalSpace"
    />
  </>;
};
