import {Dayjs} from 'dayjs';

export interface GameState {
  readonly gameName: string;
  readonly avatarUrl: string;
  readonly isProfileCreated: boolean;
  readonly isBotConnected: boolean;
  readonly isMainStoryViewed: boolean;
  readonly isFinalStoryViewed: boolean;
  readonly isFinalPageViewed: boolean;
  readonly nickname: string;
  readonly email: string;
  readonly isNickNameSet: boolean;
  readonly linkToBot: string;
  readonly linkToHelp: string;
  readonly currentDayNumber: number;
  readonly days: Array<DayInfo>;
  readonly finalEventStartTime: Dayjs;
}

export enum DayStatus {
  NotStarted,
  Solved,
  InProgress,
  Future,
}

export interface DayInfo {
  readonly isDailyStoryViewed: boolean;
  readonly isPasscodeEntered: boolean;
  readonly status: DayStatus;
  readonly startTime: Dayjs;
  readonly dayNumber: number;
  readonly tasks: Array<TaskInfo>;
  readonly totalTasks: number;
  readonly isTeamTaskSolved: boolean; // TODO: consider to remove as unused
  readonly isTeamTaskCompletionViewed: boolean;
  readonly inventoryItems: Array<InventoryItem>;
}

export interface InventoryItem {
  readonly isAvailable: boolean;
  readonly isUsed: boolean;
  readonly name: string;
  readonly resource: string;
}

export enum TaskType {
  Vr = 'vr_gallery',
  Ar = 'ar_video_on_marker',
  Image = 'image',
  Text = 'text',
  TextAndImage = 'text_and_image',
  Url = 'text_url',
  Video = 'video',
}

export interface TaskInfo {
  readonly id: number;
  readonly type: TaskType;
  readonly isTeamTask: boolean;
  readonly areHintsAvailable: boolean;
  readonly hints: Array<any>;
  readonly hintsTotal: number;
  readonly hintsUsed: number;
  readonly resources: Array<string>;
  readonly isSkipped: boolean;
  readonly isSolved: boolean;
  readonly text: string;
}

interface SelfPatchBase {
  readonly key: string;
}

export interface SelfPatchModel extends SelfPatchBase {
  readonly value: string | boolean;
}

export interface SelfPatchPasswordModel extends SelfPatchBase{
  readonly value: {
    old: string,
    new: string,
  };
}
