import React, {FC} from 'react';
import {useHistory} from 'react-router-dom';
import classNames from 'classnames';
import {Routes} from '../../app/routes';
import {InventoryItem} from '../../api/game.types';
import {ReactComponent as CheckedIcon} from '../../images/checked.svg';
import {ImageResource} from './ImageResource.component';
import styles from './Inventory.module.scss';

export enum InventorySize {
  Small,
  Large,
}

interface InventoryProps {
  readonly inventory: InventoryItem;
  readonly size: InventorySize;
}

export const Inventory: FC<InventoryProps> = ({inventory, size}) => {
  const history = useHistory();

  return (
    <div
      className={classNames(
        styles.inventory,
        {
          [styles.used]: inventory.isUsed,
          [styles.small]: size === InventorySize.Small,
          [styles.large]: size === InventorySize.Large,
        }
      )}
      onClick={() => history.push({
        pathname: Routes.InventoryDetails,
        state: {inventory},
      })}
    >
      <ImageResource
        url={inventory.resource}
        additionalClasses={classNames(
          styles.inventoryImage,
          {
            [styles.inventoryImageUsed]: inventory.isUsed,
            [styles.smallImage]: size === InventorySize.Small,
            [styles.largeImage]: size === InventorySize.Large,
          }
        )}
      />
      {inventory.isUsed && <CheckedIcon className={styles.checkedIcon}/>}
    </div>
  );
};
