import React, {ChangeEvent, FC, FormEvent, useRef} from 'react';
import classNames from 'classnames';
import {ReactComponent as CheckedIcon} from '../images/checked.svg';
import {Message, MessageType} from './Message.component';
import {AnswerResult} from '../pages/home/Mission.store';
import {TextInput} from './TextInput.component';
import styles from './Answer.module.scss';

interface AnswerProps {
  readonly answer: string;
  readonly onChange: (event: ChangeEvent<HTMLInputElement>) => void;
  readonly placeholder?: string;
  readonly onSubmit: () => void;
  readonly answerResult: AnswerResult | null;
}

export const Answer: FC<AnswerProps> = ({
  answer,
  onChange,
  onSubmit,
  placeholder,
  answerResult,
}) => {
  const ref = useRef<HTMLInputElement>(null);

  const handleSubmit = (event: FormEvent) => {
    event.preventDefault();
    onSubmit && onSubmit();
    if (ref && ref.current) {
      ref.current.blur();
    }
  };

  return (
    <div className={styles.answerContainer}>
      {!!answerResult && (
        <Message type={answerResult.isCorrect ? MessageType.warning : MessageType.error}>
          {answerResult.message}
        </Message>
      )}
      <form onSubmit={handleSubmit}>
        <TextInput
          value={answer}
          onChange={onChange}
          placeholder={placeholder}
          inputRef={ref}
          addonAfter={(
            <button className={styles.answerButton} onClick={onSubmit} type="submit">
              <CheckedIcon className={classNames(styles.answerButtonIcon, {
                [styles.correctButtonIcon]: !!answerResult && answerResult.isCorrect,
              })}
              />
            </button>
          )}
          additionalClasses={classNames(styles.answerInput, {
            [styles.correctAnswerInput]: !!answerResult && answerResult.isCorrect,
          })}
        />
      </form>
    </div>
  );
};
