import {useMutation, useQuery, useQueryClient} from 'react-query';
import {getAvatars} from '../../api/Resources.api';
import {patchSelf} from '../../api/Game.api';
import {useGameStore} from '../Game.store';

interface AvatarListStore {
  readonly avatarUrlList: Array<string>;
  readonly currentAvatar: string;
  readonly changeAvatar: (avatar: string) => void;
  readonly isAvatarListStoreFetching: boolean;
}

export const useAvatarListStore = (): AvatarListStore => {
  const queryClient = useQueryClient();
  const {gameState, isGameStoreFetching} = useGameStore();
  const {data, isFetching} = useQuery<Array<string>>('avatars', getAvatars, {staleTime: Infinity});
  const selfMutation = useMutation(patchSelf, {
    onSuccess: () => {
      queryClient.invalidateQueries('game');
      queryClient.invalidateQueries('teamPlayers');
    },
  });

  const tryChangeAvatar = (avatar: string): void => {
    selfMutation.mutate({key: 'avatar', value: avatar});
  };

  return {
    isAvatarListStoreFetching: isGameStoreFetching || isFetching || selfMutation.isLoading,
    avatarUrlList: data || [],
    currentAvatar: gameState.avatarUrl,
    changeAvatar: tryChangeAvatar,
  };
};
