import React, {FC} from 'react';
import classNames from 'classnames';
import styles from './AvatarIcon.module.scss';

export enum AvatarIconTypes {
  Round,
  RoundBig,
  Square,
}

interface AvatarIconProps {
  readonly image?: string;
  readonly type: AvatarIconTypes;
  readonly onClick?: () => void;
  readonly additionalClasses?: string;
}

export const AvatarIcon: FC<AvatarIconProps> = ({image, type, onClick, additionalClasses}) => {
  if (!image) {
    return (
      <span
        className={classNames(styles.avatarIcon, additionalClasses, {
          [styles.round]: type === AvatarIconTypes.Round,
          [styles.roundBig]: type === AvatarIconTypes.RoundBig,
          [styles.square]: type === AvatarIconTypes.Square,
        })}
      />
    );
  }

  return (
    <img
      src={image}
      alt=""
      onClick={onClick}
      className={classNames(styles.avatar, additionalClasses, {
        [styles.round]: type === AvatarIconTypes.Round,
        [styles.square]: type === AvatarIconTypes.Square,
      })}
    />
  );
};
