import React, {ChangeEvent, FC, useEffect} from 'react';
import {useHistory} from 'react-router-dom';
import classNames from 'classnames';
import {Routes} from '../../app/routes';
import {DayStatus} from '../../api/game.types';
import {Screen} from '../../common/Screen.component';
import {Title} from '../../common/Title.component';
import {Answer} from '../../common/Answer.component';
import {Button, ButtonTypes} from '../../common/Button.component';
import {TrackingType, useTimeTrackingStore} from '../common/TimeTracking.store';
import {ReactComponent as ArrowIcon} from '../../images/round-arrow.svg';
import {useGameStore} from '../Game.store';
import {Task} from './Task.component';
import {useMissionStore} from '../home/Mission.store';
import {useTeamStore} from '../common/Team.store';
import styles from './Mission.module.scss';

export const Mission: FC = () => {
  const history = useHistory();
  const {currentDay, isGameStoreFetching} = useGameStore();
  const {isCurrentUserCaptain, isTeamStoreFetching} = useTeamStore();
  const {
    answer,
    setAnswer,
    currentTask,
    isNextPrivateTaskAvailable,
    isPrevPrivateTaskAvailable,
    switchToPrevTask,
    switchToNextTask,
    answerTaskResult,
    answerTask,
    fetchHint,
    isMissionStoreFetching,
  } = useMissionStore();

  const isFetching = isGameStoreFetching || isTeamStoreFetching || isMissionStoreFetching;
  const isAnswerAllowed = !currentTask?.isTeamTask || isCurrentUserCaptain;

  const {isTrackingEnabled, setIsTrackingEnabled} = useTimeTrackingStore(TrackingType.TaskTime, currentTask?.id);

  useEffect(() => {
    if (!isTrackingEnabled) {
      setIsTrackingEnabled(true);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (!isFetching && currentDay.status === DayStatus.Solved) {
    history.push(Routes.Home);
    return null;
  }

  if (!isFetching && !currentDay.isDailyStoryViewed) {
    history.push({
      pathname: Routes.MissionStory,
      state: {dayNumber: currentDay.dayNumber},
    });
    return null;
  }

  const handleAnswerTask = () => {
    if (currentTask) {
      answerTask(currentDay.dayNumber, currentTask, answer);
    }
  };

  return (
    <Screen
      isFetching={isFetching}
      footerWithGradient={isAnswerAllowed}
    >
      <Screen.Header backTo={Routes.Home}>
        <div className={styles.header}>
          <Title title={`Mission ${currentDay.dayNumber}`}/>
          <div className={styles.headerActions}>
            {currentTask?.areHintsAvailable && (
              <Button
                title="Hint"
                type={ButtonTypes.Dark}
                onClick={() => fetchHint(currentDay.dayNumber, currentTask.id, currentTask.hints.length)}
              />
            )}
            {isPrevPrivateTaskAvailable && (
              <button className={styles.taskButton} onClick={() => {switchToPrevTask();}}>
                <ArrowIcon className={classNames(styles.taskButtonIcon, styles.prevTaskButton)}/>
              </button>
            )}
            {isNextPrivateTaskAvailable && (
              <button className={styles.taskButton} onClick={() => {switchToNextTask();}}>
                <ArrowIcon className={classNames(styles.taskButtonIcon, styles.nextTaskButton)}/>
              </button>
            )}
          </div>
        </div>
      </Screen.Header>
      <Screen.Content additionalClasses={styles.missionContent}>
        <Task task={currentTask}/>
      </Screen.Content>
      {!isFetching && !isAnswerAllowed
        ? null
        : (
          <Screen.Footer centered additionalClasses={styles.missionFooter}>
            <Answer
              answer={answer}
              onChange={(event: ChangeEvent<HTMLInputElement>) => setAnswer(event.target.value)}
              onSubmit={handleAnswerTask}
              answerResult={answerTaskResult}
              placeholder="Type your answer..."
            />
          </Screen.Footer>
        )}
    </Screen>
  );
};
