import React, {FC} from 'react';
import {InventoryItem} from '../../api/game.types';
import {Inventory, InventorySize} from '../common/Inventory.component';
import styles from './InventoryList.module.scss';

const MAX_PLACEHOLDERS = 4;

interface InventoryListProps {
  readonly inventoryItems: Array<InventoryItem>;
}

export const InventoryList: FC<InventoryListProps> = ({inventoryItems}) => {
  const placeholders = [];
  for (let i = 0; i < MAX_PLACEHOLDERS - inventoryItems.length; i++) {
    placeholders.push(<div key={i} className={styles.inventoryPlaceholder}/>);
  }

  if (inventoryItems.length === 0) {
    return (
      <div className={styles.inventoryList}>
        <div className={styles.noInventoryMessage}>
          You don’t have any inventory yet
        </div>
        <div className={styles.inventoryItems}>
          {placeholders}
        </div>
      </div>
    );
  }

  return (
    <div className={styles.inventoryList}>
      <div className={styles.inventoryWrapper}>
        <div className={styles.inventoryItems}>
          {
            inventoryItems.map((inventory, index) => (
              <div key={index} className={styles.inventoryItem}>
                <Inventory inventory={inventory} size={InventorySize.Large}/>
              </div>
            ))
          }
          {
            placeholders.length > 0 && (
              <>
                {placeholders}
              </>
            )
          }
        </div>
      </div>
    </div>
  );
};
