import React, {FC} from 'react';
import {useHistory} from 'react-router-dom';
import {Routes} from '../app/routes';
import {useGameStore} from './Game.store';
import {Screen} from '../common/Screen.component';

export const Game: FC = () => {
  const history = useHistory();
  const {gameState, isGameStoreFetching} = useGameStore();

  if (isGameStoreFetching) {
    return (
      <Screen isFetching={true} withoutHeader>
        <Screen.Content/>
      </Screen>
    );
  }

  if (!gameState.isProfileCreated) {
    history.push(Routes.OnboardingPersonalize);
    return null;
  }

  if (!gameState.isBotConnected) {
    history.push(Routes.OnboardingTeam);
    return null;
  }

  if (!gameState.isMainStoryViewed) {
    history.push(Routes.OnboardingMainStory);
    return null;
  }

  history.push(Routes.Home);

  return null;
};
