import React, {
  ChangeEvent,
  ChangeEventHandler,
  FC,
  FocusEvent,
  FocusEventHandler,
  ReactNode,
  Ref,
  useState
} from 'react';
import classNames from 'classnames';
import {Label} from './Label.component';
import {ReactComponent as ShowPasswordIcon} from '../images/eye_closed.svg';
import {ReactComponent as HidePasswordIcon} from '../images/eye_opened.svg';
import styles from './TextInput.module.scss';

interface TextInputProps {
  value?: string;
  inputRef?: Ref<HTMLInputElement>;
  onChange?: ChangeEventHandler<HTMLInputElement>;
  onBlur?: FocusEventHandler<HTMLInputElement>;
  type?: 'text' | 'password';
  placeholder?: string;
  disabled?: boolean;
  label?: string;
  name?: string;
  isValid?: boolean;
  addonAfter?: ReactNode;
  additionalClasses?: string;
}

export const TextInput: FC<TextInputProps> = ({
  type = 'text',
  inputRef,
  value,
  onChange,
  onBlur,
  placeholder,
  label,
  name,
  isValid = true,
  disabled = false,
  addonAfter,
  additionalClasses,
}) => {
  const [isPasswordVisible, setIsPasswordVisible] = useState(false);
  const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
    !!onChange && onChange(event);
  };

  const handleBlur = (event: FocusEvent<HTMLInputElement>) => {
    !!onBlur && onBlur(event);
  };

  const PasswordIcon = isPasswordVisible ? HidePasswordIcon : ShowPasswordIcon;

  return (
    <>
      {label && (
        <Label htmlFor={`id_${name}`} label={label}/>
      )}
      <div className={classNames(styles.inputContainer, additionalClasses)}>
        <input
          className={classNames(
            styles.textInput,
            {
              [styles.hasErrors]: !isValid,
              [styles.disabled]: disabled,
              [styles.password]: type === 'password',
            },
          )}
          ref={inputRef}
          value={value}
          onChange={handleChange}
          onBlur={handleBlur}
          type={isPasswordVisible ? 'text' : type}
          placeholder={placeholder}
          disabled={disabled}
          id={`id_${name}`}
        />
        {type === 'password' && (
          <PasswordIcon
            onClick={() => setIsPasswordVisible(prev => !prev)}
            className={styles.passwordIcon}
          />
        )}
        {addonAfter && (
          <div className={styles.addonAfter}>
            {addonAfter}
          </div>
        )}
      </div>
    </>
  );
};
