import {useState} from 'react';
import {useQuery} from 'react-query';
import {
  logTaskSpentTimeRequest,
  logTotalSpentTimeRequest,
  TRACKING_TIME_AMOUNT_SECONDS
} from '../../api/TimeTracking.api';

export enum TrackingType {
  TotalTime,
  TaskTime,
}

interface TimeTrackingStore {
  readonly isTrackingEnabled: boolean;
  readonly setIsTrackingEnabled: (isEnabled: boolean) => void;
}

export const useTimeTrackingStore = (type: TrackingType, taskId?: number): TimeTrackingStore => {
  const [isTrackingEnabled, setIsTrackingEnabled] = useState(false);
  useQuery('total-time', logTotalSpentTimeRequest, {
    refetchInterval: TRACKING_TIME_AMOUNT_SECONDS * 1000,
    refetchOnMount: false,
    staleTime: Infinity,
    enabled: false, //type === TrackingType.TotalTime && isTrackingEnabled,
  });
  useQuery(['task-time', taskId], () => logTaskSpentTimeRequest(taskId), {
    refetchInterval: TRACKING_TIME_AMOUNT_SECONDS * 1000,
    refetchOnMount: false,
    staleTime: Infinity,
    enabled: false,//type === TrackingType.TaskTime && isTrackingEnabled,
  });

  return {
    isTrackingEnabled,
    setIsTrackingEnabled,
  };
};
