import {useState} from 'react';
import {useMutation, useQueryClient} from 'react-query';
import {useHistory} from 'react-router-dom';
import {Routes} from '../../app/routes';
import {useGameStore} from '../Game.store';
import {patchPasswordRequest, patchSelf} from '../../api/Game.api';
import {useAvatarStore} from './Avatar.store';

export type ProfileErrorKey = 'nickname' | 'oldPassword' | 'newPassword';

export interface ProfileError {
  readonly field: ProfileErrorKey;
  readonly message: string;
}

export interface ProfileStore {
  readonly isProfileStoreFetching: boolean;
  readonly email: string;
  readonly nickname: string;
  readonly changeNickname: (nickname: string) => void;
  readonly avatar?: string;
  readonly changePassword: (oldPassword: string, newPassword: string, onSuccess: () => void) => void;
  readonly markProfileCreated: () => void;
  readonly passwordWasChanged: boolean;
  readonly error: ProfileError | null;
}

export const useProfileStore = (): ProfileStore => {
  const queryClient = useQueryClient();
  const history = useHistory();
  const [passwordWasChanged, setPasswordWasChanged] = useState(false);
  const {gameState, isGameStoreFetching} = useGameStore();
  const {avatar, isAvatarStoreFetching} = useAvatarStore(gameState.avatarUrl);
  const selfMutation = useMutation(patchSelf, {
    onSuccess: () => {
      queryClient.invalidateQueries('game');
      queryClient.invalidateQueries('teamPlayers');
    },
  });
  const passwordMutation = useMutation(patchPasswordRequest, {
    onSuccess: () => {
      setPasswordWasChanged(true);
      queryClient.invalidateQueries('game');
    },
    onError: () => {
      setError({
        field: 'oldPassword',
        message: 'Incorrect password',
      });
    },
  });
  const [error, setError] = useState<ProfileError | null>(null);

  const tryChangeNickname = (nickname: string): void => {
    const newNickname = nickname.trim();
    setError(null);
    if (newNickname.length === 0) {
      setError({
        field: 'nickname',
        message: 'Please enter username',
      });
    } else if (newNickname.length > 30) {
      setError({
        field: 'nickname',
        message: 'Username must be less than 30 characters',
      });
    } else {
      selfMutation.mutate({key: 'nickname', value: newNickname});
    }
  };

  const markProfileCreated = (): void => {
    selfMutation.mutateAsync({key: 'profile_set', value: true})
      .then(() => {
        history.push(Routes.Game);
      });
  };

  const tryChangePassword = (oldPassword: string, newPassword: string, onSuccess: () => void): void => {
    setError(null);

    if (oldPassword.length < 8) {
      setError({
        field: 'oldPassword',
        message: 'Password must contain at least 8 characters.',
      });
      return;
    }

    const updatedPassword = newPassword.trim();
    if (updatedPassword.length === 0) {
      setError({
        field: 'newPassword',
        message: 'Password should not be empty',
      });
      return;
    }

    if (updatedPassword.length < 8) {
      setError({
        field: 'newPassword',
        message: 'Password must contain at least 8 characters.',
      });
      return;
    }

    passwordMutation.mutateAsync({
      key: 'password', value: {
        old: oldPassword,
        new: updatedPassword,
      }
    }).then( () => onSuccess());
  };

  return {
    isProfileStoreFetching: isGameStoreFetching || isAvatarStoreFetching || selfMutation.isLoading || passwordMutation.isLoading,
    email: gameState.email,
    nickname: gameState.nickname,
    changeNickname: tryChangeNickname,
    avatar,
    markProfileCreated,
    changePassword: tryChangePassword,
    passwordWasChanged,
    error,
  };
};
