import axios from 'axios';
import dayjs from 'dayjs';
import {
  DayInfo,
  DayStatus,
  GameState,
  InventoryItem,
  SelfPatchModel,
  SelfPatchPasswordModel,
  TaskInfo
} from './game.types';

export const selfRequest = () =>
  axios.get('/self')
    .then(({data}) => gameStateMapper(data))
    .catch(error => {
      throw error.response.data;
    });

export const patchSelf = ({key, value}: SelfPatchModel) => {
  const valueKey = typeof value === 'boolean' ? 'value_bool' : 'value_str';
  return axios.patch('/self', {
    key,
    [valueKey]: value,
  })
    .catch(error => {
      throw error.response.data;
    });
};

export const patchPasswordRequest = ({key, value}: SelfPatchPasswordModel) => {
  return axios.patch('/self', {
    key,
    'value_str_old': value.old,
    'value_str': value.new,
  })
    .catch(error => {
      throw error.response.data;
    });
};

export const finalQuestionnaireRequest = () =>
  axios.get('/games/self/final-questionaire')
    .then(({data}) => data['questionnaire_url'])
    .catch(error => {
      throw error.response.data;
    });

export const finalPageViewedRequest = () => {
  return axios.patch('/self', {
    key: 'final_page_viewed',
    value_bool: true,
  })
    .catch(error => {
      throw error.response.data;
    });
};


const gameStateMapper = (data: any): GameState => ({
  gameName: data['game_name'],
  avatarUrl: data['avatar'],
  isProfileCreated: data['profile_set'],
  isBotConnected: data['bot_connected'],
  isMainStoryViewed: data['main_story_viewed'],
  isFinalStoryViewed: data['final_story_viewed'],
  isFinalPageViewed: data['final_page_viewed'],
  nickname: data['nickname'],
  email: data['email'],
  isNickNameSet: data['nickname_set'],
  linkToHelp: data['assistant_url'],
  linkToBot: data['bot_chat_url'],
  currentDayNumber: data['day_number'],
  finalEventStartTime: data['final_event_start_time'] && dayjs(data['final_event_start_time']),
  days: !data['days'] ? [] : data['days'].map((day: any, index: number): DayInfo => ({
    isDailyStoryViewed: day['day_story_viewed'],
    isPasscodeEntered: day['passcode_entered'],
    status: getDayStatus(day, index + 1, data['day_number']),
    startTime: day['begin_daytime'] && dayjs(day['begin_daytime']),
    dayNumber: index + 1,
    totalTasks: day['total_tasks'],
    isTeamTaskSolved: day['team_task_solved'],
    isTeamTaskCompletionViewed: day['team_task_completion_viewed'],
    inventoryItems: !day['inventory_items'] ? [] : day['inventory_items'].map((inventory: any): InventoryItem => ({
      isAvailable: inventory['available'],
      isUsed: day['team_task_solved'],
      name: inventory['name'],
      resource: inventory['resource'],
    })),
    tasks: !day['tasks'] ? [] : day['tasks'].map((task: any): TaskInfo => (({
      id: task['id'],
      type: task['content_type'],
      isTeamTask: task['is_team_mission'],
      areHintsAvailable: task['wrong_answer_attempts'] > 0 && task['hints_total_number'] > task['hints_used'],
      hints: task['hints'],
      hintsTotal: task['hints_total_number'],
      hintsUsed: task['hints_used'],
      resources: task['resources'],
      isSkipped: task['skipped'],
      isSolved: task['solved'],
      text: task['text'],
    }))),
  })),
});

const getDayStatus = (day: any, dayNumber: number, currentDay: number): DayStatus => {
  const beginDayTime = !day['begin_daytime'] ? null : dayjs(day['begin_daytime']);

  if (currentDay === 0 && dayNumber === 1) {
    return DayStatus.NotStarted;
  } else if (beginDayTime && dayjs() < beginDayTime) {
    return DayStatus.NotStarted;
  } else if (day['team_task_solved'] && currentDay === dayNumber) {
    const areAllPersonalTasksSolved = !day['tasks'] ? false : day['tasks'].every((task: any) => task['solved']);
    return areAllPersonalTasksSolved ? DayStatus.Solved : DayStatus.InProgress;
  } else if (currentDay === dayNumber) {
    return DayStatus.InProgress;
  } else if (currentDay < dayNumber) {
    return DayStatus.Future;
  } else {
    return DayStatus.Solved;
  }
};
