import {useState} from 'react';
import {useMutation, useQuery, useQueryClient} from 'react-query';
import {getUserId} from '../../lib/token';
import {getTeamInfoRequest, getTeamRequest, patchTeamRequest} from '../../api/Team.api';
import {Team, TeamInfo, TeamPlayer} from '../../api/team.types';
import {useGameStore} from '../Game.store';

export type TeamErrorKey = 'teamName';

export interface TeamError {
  readonly field: TeamErrorKey;
  readonly message: string;
}

export interface TeamStore {
  readonly teamName: string;
  readonly progress: number;
  readonly changeTeamName: (name: string) => void;
  readonly validateTeamName: (name: string) => Promise<string>;
  readonly teamPlayers: Array<TeamPlayer>;
  readonly isCurrentUserCaptain: boolean;
  readonly isTeamStoreFetching: boolean;
  readonly error: TeamError | null;
}

export const useTeamStore = (): TeamStore => {
  const queryClient = useQueryClient();
  const {gameState, isGameStoreFetching} = useGameStore();
  const [error, setError] = useState<TeamError | null>(null);
  const {
    data: teamInfo,
    isFetching: isTeamFetching,
  } = useQuery<TeamInfo>('team', getTeamInfoRequest, {
    staleTime: 30 * 1000,
  });
  const {
    data: team,
    isFetching: isTeamPlayersFetching,
  } = useQuery<Team>('teamPlayers', getTeamRequest, {
    staleTime: Infinity,
  });
  const patchTeamMutation = useMutation(patchTeamRequest, {
    onSuccess: () => queryClient.invalidateQueries('team'),
  });

  const validateTeamName = (name: string): Promise<string> => {
    const newName = name.trim();
    let error: TeamError | null = null;

    if (newName.length === 0) {
      error = {
        field: 'teamName',
        message: 'Team name should not be empty',
      };
    }
    if (newName.length > 30) {
      error = {
        field: 'teamName',
        message: 'Team name must be less than 30 characters',
      };
    }

    return new Promise<string>((resolve, reject) => {
      if (!error) {
        resolve(newName);
      } else {
        reject(error);
      }
    });
  };

  const tryChangeName = (name: string): void => {
    const newName = name.trim();
    validateTeamName(newName)
      .then(value => {
        setError(null);
        patchTeamMutation.mutate({key: 'name', value});
      })
      .catch(error => {
        setError(error);
      });
  };

  return {
    isTeamStoreFetching: isTeamFetching || isTeamPlayersFetching || isGameStoreFetching || patchTeamMutation.isLoading,
    teamName: teamInfo?.name || '',
    progress: !teamInfo
      ? 0
      : getProgress(teamInfo, gameState.currentDayNumber - 1),
    changeTeamName: tryChangeName,
    validateTeamName,
    teamPlayers: team ? team.players : [],
    isCurrentUserCaptain: team && team.captainId ? team.captainId === getUserId() : true,
    error,
  };
};

const getProgress = (teamInfo: TeamInfo, currentDayNumber: number): number => {
  const currentDayInfo = teamInfo.days[currentDayNumber];

  if (currentDayInfo) {
    return Math.floor(currentDayInfo.totalTasksSolved / currentDayInfo.totalTasks * 100);
  }

  return 0;
};
