import React, {ChangeEvent, FC} from 'react';
import {useHistory} from 'react-router-dom';
import {Routes} from '../../app/routes';
import {DayInfo, DayStatus} from '../../api/game.types';
import {Answer} from '../../common/Answer.component';
import {Button, ButtonTypes} from '../../common/Button.component';
import {DailyInventoryItems} from './DailyInventoryItems.component';
import {useMissionStore} from './Mission.store';
import styles from './Day.module.scss';

interface DayProps {
  readonly day: DayInfo;
}

export const Day: FC<DayProps> = ({day}) => {
  const history = useHistory();
  const {answer, setAnswer, unlockDay, unlockResult} = useMissionStore();

  if (day.status === DayStatus.Solved) {
    return (
      <div>
        <DailyInventoryItems inventoryItems={day.inventoryItems}/>
        <div className="centerContent">
          <Button
            title="Continue"
            onClick={() => history.push({
              pathname: Routes.MissionStory,
              state: {dayNumber: day.dayNumber},
            })}
            type={ButtonTypes.Main}
          />
        </div>
      </div>
    );
  }

  if (day.status === DayStatus.NotStarted) {
    return (
      <div className="centerContent">
        <Button
          title={`Starts at ${day.startTime.format('lll')}`}
          type={ButtonTypes.Secondary}
        />
      </div>
    );
  }

  if (day.isPasscodeEntered) {
    return (
      <div className={styles.dayMission}>
        <DailyInventoryItems inventoryItems={day.inventoryItems}/>
        <div className="centerContent">
          <Button
            title="Continue"
            type={ButtonTypes.Main}
            onClick={() => history.push(Routes.Mission)}
          />
        </div>
      </div>
    );
  }

  return (
    <Answer
      answer={answer}
      onChange={(event: ChangeEvent<HTMLInputElement>) => setAnswer(event.target.value)}
      onSubmit={() => unlockDay(day.dayNumber, answer)}
      answerResult={unlockResult}
      placeholder="Type passcode..."
    />
  );
};
