import React, {FC, ReactNode, useState} from 'react';
import classNames from 'classnames';
import {ReactComponent as ArrowIcon} from '../images/gt.svg';
import {ReactComponent as CheckedIcon} from '../images/checked.svg';
import {ReactComponent as UnCheckedIcon} from '../images/unchecked.svg';
import styles from './Accordion.module.scss';

interface AccordionCompositionProps {
  Panel: typeof AccordionPanel;
}

interface AccordionProps {
}

export const Accordion: FC<AccordionProps> & AccordionCompositionProps = ({
  children,
}) => {
  return (
    <div className={styles.accordion}>
      {children}
    </div>
  );
};

export enum AccordionPanelTypes {
  Default,
  Mission,
}

interface AccordionPanelProps {
  readonly title: string;
  readonly isExpandedByDefault?: boolean;
  readonly icon?: ReactNode;
  readonly status?: string;
  readonly checked?: boolean;
  readonly type?: AccordionPanelTypes;
}

export const AccordionPanel: FC<AccordionPanelProps> = ({
  title,
  isExpandedByDefault = false,
  icon: Icon,
  status,
  checked,
  type = AccordionPanelTypes.Default,
  children,
}) => {
  const [isCollapsed, setIsCollapsed] = useState(!isExpandedByDefault);
  const togglePanel = () => setIsCollapsed(prev => !prev);

  return (
    <div className={classNames(styles.accordionPanel, {
      [styles.mission]: type === AccordionPanelTypes.Mission,
      [styles.collapsed]: isCollapsed,
    })}
    >
      <div className={styles.header} onClick={togglePanel}>
        <div className={styles.headerLeft}>
          {Icon ? (
            <span className={styles.icon}>{Icon}</span>
          ) : (
            checked
              ? (<CheckedIcon className={styles.checkedIcon}/>)
              : (<UnCheckedIcon className={classNames(styles.uncheckedIcon, {
                [styles.collapsedUncheckedIcon]: isCollapsed,
              })}/>)
          )}
          <span className={styles.title}>{title}</span>
        </div>
        <div className={styles.headerRight}>
          {status && (
            <span className={styles.status}>{status}</span>
          )}
          <ArrowIcon className={classNames(styles.arrowIcon, {
            [styles.collapsedArrowIcon]: isCollapsed,
          })}
          />
        </div>
      </div>
      <div className={classNames(styles.content, {
        [styles.hidden]: isCollapsed,
      })}
      >{children}</div>
    </div>
  );
};

Accordion.Panel = AccordionPanel;
