import React, {FC} from 'react';
import {useHistory} from 'react-router-dom';
import {Routes} from '../../app/routes';
import {Progress} from '../../common/Progress.component';
import {useTeamStore} from '../common/Team.store';
import {ReactComponent as GtIcon} from '../../images/gt.svg';
import styles from './GameInfo.module.scss';

export const GameInfo: FC = () => {
  const history = useHistory();
  const {teamName, teamPlayers, progress} = useTeamStore();

  return (
    <div className={styles.gameInfo}>
      <div className={styles.teamInfo}>
        <div className={styles.teamName}>Team: {teamName}</div>
        <div className={styles.teamMembers} onClick={() => history.push(Routes.ProfileTeam)}>
          {teamPlayers.length} members
          <GtIcon className={styles.gtIcon}/>
        </div>
      </div>
      <div className={styles.gameProgress}>
        <Progress progress={progress}/>
      </div>
    </div>
  );
};
