import {useEffect, useState} from 'react';
import {useHistory} from 'react-router-dom';
import {useMutation, useQueryClient} from 'react-query';
import {
  answerTaskRequest,
  getHintRequest,
  skipTaskRequest,
  teamTaskCompletionViewedRequest,
  unlockDayRequest
} from '../../api/Mission.api';
import {TaskInfo} from '../../api/game.types';
import {Routes} from '../../app/routes';
import {useGameStore} from '../Game.store';

const ANSWER_RESULT_DELAY = 3000;
const MAX_NUMBER_OF_SKIPPED_TASKS = 1;

const CORRECT_ANSWERS = [
  'Correct',
  'Super Correct!',
  'Well done!',
  'You are right',
  'Awesome! Fine job you did!',
];

const WRONG_ANSWERS = [
  'Incorrect',
  'Think more time',
  'Not right',
  'Wrong answer',
  'Absolutely wrong',
];

const getRandomMessage = (messages: Array<string>): string => {
  const randomIndex = Math.floor(Math.random() * messages.length);
  return messages[randomIndex];
};

interface MissionStore {
  readonly isMissionStoreFetching: boolean;
  readonly answer: string;
  readonly setAnswer: (answer: string) => void;
  readonly unlockDay: (dayNumber: number, answer: string) => void;
  readonly unlockResult: AnswerResult | null;
  readonly answerTask: (dayNumber: number, task: TaskInfo, answer: string) => void;
  readonly answerTaskResult: AnswerResult | null;
  readonly currentTask: TaskInfo | null;
  readonly isNextPrivateTaskAvailable: boolean;
  readonly switchToNextTask: () => void;
  readonly isPrevPrivateTaskAvailable: boolean;
  readonly switchToPrevTask: () => void;
  readonly markTeamTaskCompletionViewed: () => void;
  readonly fetchHint: (dayNumber: number, taskId: number, hintNumber: number) => void;
}

export interface AnswerResult {
  readonly isCorrect: boolean;
  readonly message: string;
}

export const useMissionStore = (): MissionStore => {
  const queryClient = useQueryClient();
  const [answer, setAnswer] = useState('');
  const history = useHistory();
  const {currentDay, isGameStoreFetching} = useGameStore();
  const [unsolvedPrivateTasks, setUnsolvedPrivateTasks] = useState<Array<TaskInfo>>([]);
  const [teamTask, setTeamTask] = useState<TaskInfo | null>(null);
  const [currentTaskIndex, setCurrentTaskIndex] = useState(0);
  const [unlockResult, setUnlockResult] = useState<AnswerResult | null>(null);
  const [answerTaskResult, setAnswerTaskResult] = useState<AnswerResult | null>(null);

  const unlockDayMutation = useMutation(unlockDayRequest);
  const answerTaskMutation = useMutation(answerTaskRequest);
  const skipTaskMutation = useMutation(skipTaskRequest);
  const markTeamTaskCompletionViewedMutation = useMutation(teamTaskCompletionViewedRequest, {
    onSuccess: () => {
      queryClient.invalidateQueries('game');
    },
  });
  const fetchHintMutation = useMutation(getHintRequest, {
    onSuccess: () => {
      queryClient.invalidateQueries('game');
    },
  });

  const skippedTasksCount = unsolvedPrivateTasks.filter(({isSkipped}) => isSkipped).length;
  const solvedPrivateTasksCount = currentDay.tasks.filter(({isSolved, isTeamTask}) => isSolved && !isTeamTask).length;

  const isMaximumSkippedTasksLimitExceeded = MAX_NUMBER_OF_SKIPPED_TASKS <= skippedTasksCount;
  const areTherePrivateTasksToSolve = 0 !== currentDay.totalTasks - solvedPrivateTasksCount - unsolvedPrivateTasks.length - 1; // 1 is for team-task

  const isAllowToSkipTask = !isMaximumSkippedTasksLimitExceeded && areTherePrivateTasksToSolve;
  const isThereNextAvailablePrivateTask = currentTaskIndex < unsolvedPrivateTasks.length - 1;

  useEffect(() => {
    setUnsolvedPrivateTasks(
      currentDay.tasks
        .filter(({isSolved, isTeamTask}) => !isSolved && !isTeamTask)
    );
    const unsolvedTeamTasks = currentDay.tasks
      .filter(({isSolved, isTeamTask}) => !isSolved && isTeamTask);
    if (unsolvedTeamTasks.length > 0) {
      setTeamTask(unsolvedTeamTasks[0]);
    }
  }, [currentDay]);

  const tryUnlockDay = (dayNumber: number, answer: string): void => {
    if (unlockDayMutation.isLoading) {
      return;
    }

    unlockDayMutation.mutateAsync({dayNumber, answer})
      .then(({data}) => data)
      .then(({correct: isCorrect}) => {
        setUnlockResult({
          isCorrect,
          message: isCorrect ? getRandomMessage(CORRECT_ANSWERS) : getRandomMessage(WRONG_ANSWERS),
        });
        return isCorrect;
      })
      .then(isCorrect => {
        setTimeout(() => {
          if (isCorrect) {
            queryClient.invalidateQueries('game');
            history.push(Routes.Mission);
          } else {
            setUnlockResult(null);
          }
        }, ANSWER_RESULT_DELAY);
      });
  };

  const invalidateQueriesAfterAnswer = () => {
    queryClient.invalidateQueries('game');
    queryClient.invalidateQueries('team');
  };

  const tryAnswerTask = (dayNumber: number, task: TaskInfo, answer: string): void => {
    if (answer.trim().length === 0 || answerTaskMutation.isLoading) {
      return;
    }

    answerTaskMutation.mutateAsync({dayNumber, taskId: task.id, answer})
      .then((answerResult) => {
        setAnswerTaskResult({
          isCorrect: answerResult.correct,
          message: answerResult.correct ? getRandomMessage(CORRECT_ANSWERS) : getRandomMessage(WRONG_ANSWERS),
        });
        if (!answerResult.correct) {
          invalidateQueriesAfterAnswer();
        }
        return answerResult;
      })
      .then(answerResult => {
        setTimeout(() => {
          if (answerResult.correct) {
            invalidateQueriesAfterAnswer();
            if (answerResult.inventoryItems.length > 0) {
              history.push({
                pathname: Routes.ReceivedInventories,
                state: {
                  inventories: answerResult.inventoryItems,
                },
              });
              return;
            }
            setCurrentTaskIndex(0);
            if (task.isTeamTask) {
              history.push(Routes.Home);
            }
          }
          setAnswerTaskResult(null);
          setAnswer('');
        }, ANSWER_RESULT_DELAY);
      });
  };

  const switchToNextTask = (): void => {
    if (!isMaximumSkippedTasksLimitExceeded) {
      skipTaskMutation.mutateAsync({dayNumber: currentDay.dayNumber, taskId: unsolvedPrivateTasks[currentTaskIndex].id})
        .then(({skipped}) => {
          if (skipped) {
            queryClient.invalidateQueries('game');
            setCurrentTaskIndex(prev => prev + 1);
          }
        });
    } else {
      setCurrentTaskIndex(prev => prev + 1);
    }
    setAnswer('');
    setAnswerTaskResult(null);
  };

  const switchToPrevTask = (): void => {
    setCurrentTaskIndex(prev => prev - 1);
    setAnswer('');
    setAnswerTaskResult(null);
  };

  const markTeamTaskCompletionViewed = (): void => {
    markTeamTaskCompletionViewedMutation.mutateAsync(currentDay.dayNumber)
      .then(() => {
        history.push(Routes.Home);
      });
  };

  const tryFetchHint = (dayNumber: number, taskId: number, hintNumber: number): void => {
    if (fetchHintMutation.isLoading) {
      return;
    }

    fetchHintMutation.mutate({dayNumber, taskId, hintNumber});
  };

  return {
    isMissionStoreFetching: isGameStoreFetching || unlockDayMutation.isLoading || answerTaskMutation.isLoading
      || skipTaskMutation.isLoading || markTeamTaskCompletionViewedMutation.isLoading || fetchHintMutation.isLoading,
    answer,
    setAnswer,
    unlockDay: tryUnlockDay,
    unlockResult,
    answerTask: tryAnswerTask,
    answerTaskResult,
    currentTask: unsolvedPrivateTasks[currentTaskIndex] || teamTask,
    isNextPrivateTaskAvailable: isAllowToSkipTask || isThereNextAvailablePrivateTask,
    switchToNextTask,
    isPrevPrivateTaskAvailable: unsolvedPrivateTasks.length > 1 && currentTaskIndex !== 0,
    switchToPrevTask,
    markTeamTaskCompletionViewed,
    fetchHint: tryFetchHint,
  };
};
