import React, {FC} from 'react';
import {useHistory} from 'react-router-dom';
import {Screen} from '../../common/Screen.component';
import {Button, ButtonTypes} from '../../common/Button.component';
import {Panel} from '../../common/Panel.components';
import {Routes} from '../../app/routes';
import {useGameStore} from '../Game.store';
import {ReactComponent as CheckinIcon} from '../../images/checkin.svg';

export const Checkin: FC = () => {
  const history = useHistory();
  const {gameState, isGameStoreFetching} = useGameStore();

  const handleContinue = () => {
    if (gameState.isBotConnected) {
      history.push(Routes.Game);
    } else {
      window.open(gameState.linkToBot);
    }
  };

  return (
    <Screen footerWithGradient isFetching={isGameStoreFetching}>
      <Screen.Content>
        <Panel
          icon={<CheckinIcon/>}
          mainText="Check In"
          additionalText="Your journey has begun. Your first mission - should you choose to accept it - is to inform Mission HQ that you are ALL IN."
        />
      </Screen.Content>
      <Screen.Footer centered>
        <Button
          title={gameState.isBotConnected ? 'Continue' : 'Check-in here'}
          onClick={handleContinue}
          type={ButtonTypes.Main}
        />
      </Screen.Footer>
    </Screen>
  );
};
